import React, {useEffect} from 'react';
import {connect} from 'react-redux'
import {SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION} from "../../background/constant";

function PopupRemindCashbackActivation({lang, showPopupRemindActivation, dispatch}) {

    if (!showPopupRemindActivation) {
        return null;
    }

    function closePopupRemindCashbackActivation() {
        dispatch({
            type: SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION,
            value: false
        });
    }

    useEffect(() => {
        if (showPopupRemindActivation) {
            setTimeout(() => {
                closePopupRemindCashbackActivation();
            },5000);
        }
    });

    return (
        <div id="notice-5">
            <div className="mb-logo">
                <img src="https://cdn.megabonus.com/images/extension/yellow-circle-logo.svg" alt="logo"/>
            </div>
            <div className="ext-mb-content">
                <p className="ext-mb-title">
                    {lang.popup_remind_cashback}
                </p>
                <p className="ext-mb-text">
                    {lang.popup_in_15_minutes}
                </p>
            </div>
            <div className="ext-mb-close">
                <a
                    onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        // console.log('=== PopupCashbackActivated - closed 3', showPopupRemindActivation);
                        closePopupRemindCashbackActivation();
                    }}
                > </a>
            </div>
        </div>
    )
}


const mapStateToProps = state => {
    return {
        lang: state.lang,
        showPopupRemindActivation: state.topline.showPopupRemindActivation,
    }
};

export default connect(mapStateToProps)(PopupRemindCashbackActivation)