import React, {Component} from 'react';
import {connect} from "react-redux";

import chromeLib from '../../lib/BrowserApi';
import Progress from 'react-progressbar';

window.chromeLib = chromeLib;

import TopLineHelpModal from "./TopLineHelpModal";
import {
    ACTIVE_TAB,
    GET_ONBOARDING_SHOW,
    HOME,
    SET_SHOW_TOPLINE_SETTINGS_MODAL
} from "../../background/constant";
import {CONTENT_TOP_LINE} from "../../background/actions";

class TopLineActivate extends Component {
    static propTypes = {}

    static defaultProps = {}

    constructor(props) {
        super(props);

        this.state = {
            showSettingsModal: false,
            showHelpModal: false,
            activateTimeoutSeconds: 8,
            isClickedActivate: false,
        }
    }

    showModalSettings = e => {
        this.props.dispatch({
            type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
            page: window.location.href,
            value: !this.props.showSettingsModal
        })
    };
    showModalHelp = e => {
        this.setState({
            showHelpModal: !this.state.showHelpModal
        });
    };

    handleActiveClick() {
        this.setState(state => ({
            isClickedActivate: true
        }))

        this.interval = setInterval(() => this.runProgressbar(), 1000)
    }

    runProgressbar() {


        if (this.state.activateTimeoutSeconds >= 1) {
            this.setState(state => ({
                activateTimeoutSeconds: state.activateTimeoutSeconds - 1
            }))
        } else {
            clearInterval(this.interval);

            chromeLib.sendMessage(ACTIVE_TAB, {
                componentName: CONTENT_TOP_LINE
            })
        }
    }

    getProgressPercent(amount) {
        return 100 - Math.floor(amount * 100 / 8)
    }

    render() {
        // console.log('TopLineActivate: state, props', this.state, this.props);
        return (
            <div id="notice-3" style={{
                height: 'auto', top: 0, left: 0
            }}>
                {
                    this.state.isClickedActivate &&
                    <Progress completed={this.getProgressPercent(this.state.activateTimeoutSeconds)} color={'#b8d767'}
                              height={'4px'}/>
                }
                <strong className="logoAB"
                        style={{background: "url(https://cdn.megabonus.com/images/extension/gray-logo-".concat((this.props.lang.lang === 'ru' ? this.props.lang.lang : 'en')).concat(".svg) left 50% / auto 14px no-repeat")}}
                        data-spm-anchor-id="a2g0o.detail.0.i7.442c3568pjrdFc"
                        onClick={() => {
                            window.open(HOME)
                        }}
                ><a href="#"> </a></strong>
                {
                    this.state.isClickedActivate &&
                    <div className="btn-holder center-text">
                        <span className="ext-mb-title">{this.props.lang.activation_is_running}</span>
                    </div>
                }
                {
                    !this.state.isClickedActivate &&
                    <React.Fragment>
                        <div className="btn-holder">
                            <span className="ext-mb-title">{this.props.lang.you_may_get_cashback}</span>

                            <div className="btn-block">
                                <a href="#" className="ext-btn btn-activate btn-activate"
                                   onClick={() => {
                                       this.handleActiveClick()
                                   }}
                                >{this.props.lang.yes_i_want}</a>
                                <a href="#" className="not-now-activate"
                                   onClick={() => {
                                       this.props.handleHideTimeout(60 * 8)
                                   }}
                                >{this.props.lang.not_now}</a>

                            </div>

                        </div>
                        <div className="notify-block">
                            <a href="#" className="btn-notify-notice" style={{
                                background: "url(https://cdn.megabonus.com/images/user/popups/notice_icon.svg) center center / 15px 17px no-repeat"
                            }}> </a>
                            <span className="localize"
                                  data-localize="remind_in_15_minutes"
                                  onClick={() => {
                                      this.props.handleHideTimeout(15)
                                  }}
                            >{this.props.lang.remind_in_15_minutes}</span>
                        </div>
                    </React.Fragment>
                }

                {
                    !this.state.isClickedActivate &&
                    <div className="actions">
                        <a href="#" className="btn-help-notice"
                           style={
                               {
                                   background: "url(https://cdn.megabonus.com/images/extension/gray-help.svg) 0% 0% / cover no-repeat"
                               }
                           }
                           onClick={() => {
                               this.props.dispatch({
                                   type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
                                   value: false,
                                   page: window.location.href
                               })
                               this.setState({
                                   showHelpModal: !this.state.showHelpModal,
                               })
                           }}
                        > </a>

                        <TopLineHelpModal onClose={this.showModalHelp} show={this.state.showHelpModal}/>

                        <a href="#" className="btn-settings-notice"
                           style={
                               {background: "url(https://cdn.megabonus.com/images/extension/gray-settings.svg) 0% 0% / cover no-repeat"}
                           }
                           onClick={() => {
                               this.showModalSettings();
                               this.setState({
                                   showHelpModal: false
                               })
                           }}
                        />

                    </div>
                }
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        lang: state.lang,
        showSettingsModal: state.topline.showSettingsModal,
        showPopupActivated: state.topline.showPopupActivated,
    }
};

export default connect(mapStateToProps)(TopLineActivate);
