import {
  SAVE_PARCEL_TRACK
} from "../background/constant";

import eventsObserver from "./EventsObserver";
import jQuery from 'jquery';
const $ = window.jQuery = jQuery;

class ParcelActions {
  constructor () {
    this.isAliPage = false;
    this.isOrderListPage = false;
    this.isOrderDetailPage = false;
    this.isLogisticsDetailPage = false;
    this.trackNumber = null;
    this.trackName = null;
    this.orderNumber = null;

    if (window.location.origin.indexOf("aliexpress.com") > -1 || window.location.origin.indexOf("aliexpress.ru") > -1) {
      this.isAliPage = true;
    }

    if (this.isAliPage && (window.location.href.indexOf("orderList.htm") > -1 || window.location.href.indexOf("order_list.htm") > -1)) {
      this.isOrderListPage = true;
    }

    if (this.isAliPage && window.location.href.indexOf("order_detail") > -1) {
      this.isOrderDetailPage = true;
    }

    if (this.isAliPage && window.location.href.indexOf("logisticsdetail.htm") > -1) {
      this.isLogisticsDetailPage = true;
    }

  }

  init(){
    this.checkTrackNumberExists();
  }

  checkTrackNumberExists() {
    if (!this.isAliPage) {
      return false;
    }

    if (this.isOrderListPage) {
      this.processOrderListPage();
    }
    else if (this.isOrderDetailPage) {
      this.processOrderDetailPage();
    }
    else if (this.isLogisticsDetailPage) {
      this.processLogisticsDetailPage();
    }

    this.postProcessTrackNumber();
    this.postProcessTrackName();

    if (this.trackNumber) {
      this.sendTrackDataToApiWithoutParameters();
    }
  }

  processOrderListPage() {
    // cannot take track name, so get only track number
    eventsObserver.eventMutantObserver(this.callbackTrackingBlockAppear);
  }

  callbackTrackingBlockAppear(mutation) {
    if (mutation.addedNodes.length > 0) {
      let mutationElement = mutation.addedNodes[0];
      if ($(mutationElement).hasClass('ui-balloon')) {
        $(mutationElement).bind('DOMSubtreeModified', function () {
          let trackNumberMask = $(this)[0].innerHTML.match(/mail-no">(.+?)<\/span>/);
          if (trackNumberMask && trackNumberMask[1].length > 0) {
            ParcelActions.sendTrackDataToApi(trackNumberMask[1], 'Aliexpress order');
          }
        });
      }
    }
  }

  processOrderDetailPage() {
    this.findTrackNumberOnOrderDetailPage();
    this.findTrackNameOnOrderDetailPage();
  }

  findTrackNumberOnOrderDetailPage() {
    let trackNumberObject = $('.logistics-num');
    if (trackNumberObject.length > 0) {
      this.trackNumber = trackNumberObject[0].innerText.trim();
    }
  }

  findTrackNameOnOrderDetailPage() {
    let trackNameObject = $('.baobei-name');
    if (trackNameObject.length > 0) {
      this.trackName = trackNameObject[0].innerText.trim();
    }
  }

  processLogisticsDetailPage() {
    this.findTrackNumberOnLogisticsDetailPage();
    this.findTrackNameOnLogisticsDetailPage();
  }

  findTrackNumberOnLogisticsDetailPage() {
    let trackNumberObject = $('.tracking-no span');
    if (trackNumberObject.length > 0) {
      this.trackNumber = trackNumberObject[0].innerText.trim();
      return;
    }

    trackNumberObject = $('.order-item .info');
    if (trackNumberObject.length > 0) {
      this.trackNumber = trackNumberObject[0].innerText.trim();
      return;
    }
  }

  findTrackNameOnLogisticsDetailPage() {
    // we cannot take track name as item name, so take order number as alternative
    let orderNumberObject = $('.order-id .value');
    if (orderNumberObject.length > 0) {
      this.orderNumber = orderNumberObject[0].innerText.trim();
      return;
    }
  }

  postProcessTrackNumber() {
    if (this.trackNumber && this.trackNumber.length > 0) {
      let patternToLeaveOnlyLettersAndNumbers = new RegExp("[^a-zA-Z0-9,]+");
      this.trackNumber = this.trackNumber.replace(patternToLeaveOnlyLettersAndNumbers, '').substr(0, 44);
    }
  }

  postProcessTrackName() {
    if (this.trackName && this.trackName.length > 0) {
      this.trackName = this.trackName.trim();
    }
    else if (this.orderNumber) {
      this.trackName = 'Aliexpress #' + this.orderNumber.trim();
    }
    else {
      this.trackName = '';
    }

    this.trackName = this.trackName.substr(0, 49);
  }

  static sendTrackDataToApi(trackNumber, trackName = null) {

    if (!trackName) {
      trackName = '';
    }

    chromeLib.sendMessage(SAVE_PARCEL_TRACK, {
      trackName: trackName,
      trackNumber: trackNumber,
    })
  }

  sendTrackDataToApiWithoutParameters() {
    ParcelActions.sendTrackDataToApi(this.trackNumber, this.trackName);
  }
}

const parcelActions = new ParcelActions();
export default parcelActions;
