class ParsePage {

  constructor() {
    this.price = 0;
    this.title = '';
    this.description = '';
    this.img = '';
    this.img_all = [];
    this.currency = '';
    this.url = window.location.href;
    this.category = '';
    this.brand = '';
    /* reviews format
    [{
      id: 'id отзыва в внутренней системе магазина'
      username: 'nickname',
      stars: 'оценка от 0 до 5',
      text: 'текст отзыва',
      date: 'дата в формате Y:m:d',
      likes: 'количество лайков отзыву'
      images: {
          0: 'url image 1',
          1: 'url image 2',
          ...
        }
      },
      ...]
     */
    this.reviews = [];
    this.orders_count = 0;
    this.rating = 0;
    /* characteristics format
    [{
      attrName: 'Имя аттрибута',
      attrValue: 'значение аттрибута'
      },
      ...]
     */
    this.characteristics = [];
    this.seller_data = {};
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);
    return this.getProductData();
  }

  getPreviouslyCollectedData(parsedPreviouslyData) {
    if (this.isEmptyObject(parsedPreviouslyData)) {
      return;
    }
    this.price = parsedPreviouslyData.price;
    this.title = parsedPreviouslyData.title;
    this.description = parsedPreviouslyData.description;
    this.img = parsedPreviouslyData.img;
    this.img_all = parsedPreviouslyData.img_all;
    this.currency = parsedPreviouslyData.currency;
    this.url = parsedPreviouslyData.url;
    this.category = parsedPreviouslyData.category;
    this.brand = parsedPreviouslyData.brand;
    this.reviews = parsedPreviouslyData.reviews;
    this.orders_count = parsedPreviouslyData.orders_count;
    this.rating = parsedPreviouslyData.rating;
    this.characteristics = parsedPreviouslyData.characteristics;
    this.seller_data = parsedPreviouslyData.seller_data;
  }

  isEmptyObject(obj) {
    for (let i in obj) {
      if (obj.hasOwnProperty(i)) {
        return false;
      }
    }
    return true;
  }

  formatCategoryString (category) {
    // some shops send not exact category, but full tree of categories
    if (category.length > 39) {
      let categoriesList = category.split(' ');
      if (categoriesList.length > 1) {
        category = categoriesList[categoriesList.length - 1];
      }
      else {
        category = '';
      }
    }
    return category.trim();
  }

  formatPriceString (price) {
    return price.toString().replace('.0000', '').split('.00')[0].replace(',00', '').replace(/(?:\\[rnt"\s]|[\r\n\t"\s])/g, "");
  }

  getDataFromDOM (text, regex, full = false) {
    let result = text.match(regex);
    if (result) {
      if (full) {
        return result[0];
      }
      return result[1];
    }
    return '';
  }

  urlPrepare(url) {
    if (typeof url !== 'string' || url.indexOf('//') < 0 || url.length < 10) {
      return '';
    }
    if (url.length > 256) {
      url = url.substring(0,255);
    }
    if (url.indexOf('http://') < 0 && url.indexOf('https://') < 0) {
      return 'https:' + url;
    }
    return url;
  }

  getProductData () {
    this.postProcessParams();

    return {
      img: this.img,
      img_all: this.img_all,
      title: this.title,
      description: this.description,
      url: this.url,
      price: this.price,
      currency: this.currency,
      category: this.category,
      brand: this.brand,
      reviews: this.reviews,
      orders_count: this.orders_count,
      rating: this.rating,
      characteristics: this.characteristics,
      seller_data: this.seller_data,
    }
  }

  postProcessParams() {
    this.formatTextBlocks();
    this.imgPrepare();
  }

  formatTextBlocks () {
    // clear linebreaks, tabs and their duplicates
    this.title = this.title.replace(/\s+/g, ' ');
    this.description = this.description.replace(/\s+/g, ' ');
    // to lower case
    this.brand = this.brand.toLowerCase();
    this.category = this.category.toLowerCase();
  }

  imgPrepare () {
    this.img = this.cleanImg(this.img);
    if (this.img.length > 0) {
      if (this.img_all.length === 0) {

        this.img_all.push(this.img)
      }
    }
  }

  cleanImg (pathImg) {
    var url = new URL(location.href.split('?')[0])
    if (pathImg[0] !== undefined && pathImg[0] === '/' && pathImg[1] === '/') {
      return url.protocol.replace(':','').concat(':').concat(pathImg)
    }
    if (pathImg[0] !== undefined && pathImg[0] === '/') {
      return url.origin.concat(pathImg)
    }
    return pathImg;
  }

  checkDataComplete() {
    return (this.price && this.price !== "0")
        && this.currency && this.currency.length > 0
        && this.img && this.img.length > 0
        && this.title && this.title.length > 0
        && this.description
        && this.url
        && this.category
        && this.brand;
  }

}

export default ParsePage;
