import jQuery from 'jquery';
import chromeLib from "./BrowserApi";
import helper from "./Helper";
import { GET_SIMILAR_ITEMS } from "../background/constant";

const $ = window.jQuery = jQuery;

class AliSimilarItems {

  constructor () {

  }

  init(){
    // парсинг данных со страницы
    let $objectId = $('input[name="objectId"]');
    let $linkCanonical = $('link[rel="canonical"]');
    let currentItemList = (
          $objectId.attr('value')
          || (
              $linkCanonical
              && $linkCanonical.attr('href') !== undefined
              && ($linkCanonical.attr('href').match(/\/(\d+)\.\h/i))
        )
        ? $linkCanonical.attr('href').match(/\/(\d+)\.\h/i)[1]
        : null);

    if (!currentItemList) {
      return false;
    }

    let categoryId = this.parseFromStaticPage('categoryId'),
      shopId = this.parseFromStaticPage('shopId'),
      companyId = this.parseFromStaticPage('companyId');

    this.getItems({ currentItemList, categoryId, shopId, companyId })
  }

  getItems (data) {
    const uri = "https://gpsfront.aliexpress.com/getI2iRecommendingResults.do?";
    let Q = Object.assign({
      'recommendType': 'toOtherSeller',
      'scenario': 'pcDetailBottomMoreOtherSeller',
      'limit': '50',
      'currency': this.getItemCurrency()
    }, data);

    let query = Object.keys(Q).map(k => `${encodeURIComponent(k)}=${encodeURIComponent(Q[k])}`).join('&');

    chromeLib.sendMessage(GET_SIMILAR_ITEMS, {
      query: uri + query,
      itemPrice: this.getItemPrice(),
      itemCurrency: this.getItemCurrencyForUser(),
    });
  }

  getItemCurrencyForUser = () => {
    let selector = $('.product-price-value[itemprop="price"]');

    if ($('span[itemprop="priceCurrency"]').length > 0) {
      selector = $('span[itemprop="priceCurrency"]')
    }

    if (selector && selector.length > 0) {
      return selector[0].innerText.replace(/[0-9]/g, '').replace(',', '').replace('-', '').replace(/&nbsp;/gi,'').replace(/\s/g, '')
    }
    // default
    return 'USD';
  }

  getItemCurrency() {
    let current = 'RUB';

    if (window.location.origin.indexOf('tmall.aliexpress') === -1) {
      current = $('#switcher-info').find('.currency').html()
    }
    current = current === undefined ? '' : current.trim();
    return current.toLowerCase()
  }


  /**
   * Цена товара
   * @returns {number|*}
   */
  getItemPrice = () => {
    // Если Tmall
    if (window.location.origin.indexOf('tmall.aliexpress') !== -1) {
      let tmallSelector = $('#j-sku-price[itemprop="price"]');
      if (tmallSelector.length === 0) {
        tmallSelector = $('#j-sku-discount-price');
      }

      if (tmallSelector.length === 0) {
        return 0;
      }
      return helper.parseFloats(tmallSelector.html())
    }

    if ($('#j-sku-price').find('span[itemprop="lowPrice"]').length > 0) {
      return helper.parseFloats($('#j-sku-price').find('span[itemprop="lowPrice"]').first().html())
    } else {
      // console.log(213)
    }

    // Если новый дизайн
    if ($('span.product-price-value[itemprop="price"]').length > 0) {
      return helper.parseFloats($('span.product-price-value[itemprop="price"]').first().html())
    }

    if ($('#j-sku-discount-price').length > 0) {
      return helper.parseFloats($('#j-sku-discount-price').first().html())
    }

    return 0;
  };

  parseFromStaticPage (param) {
    param = param === undefined ? '' : param.trim();

    let pattern = 'window\.runParams\.' + param + '="(\\d{0,20})"',
      re = new RegExp(pattern);

    let doc = $(document).find('body').html();

    return doc.match(re) ? doc.match(re)[1] : null
  }
}

const aliSimilarItems = new AliSimilarItems();
export default aliSimilarItems;
