import React from 'react';
import {render} from 'react-dom';
import {Provider} from 'react-redux';

import {Store} from 'webext-redux';
import patchDeepDiff from 'webext-redux/lib/strategies/deepDiff/patch';
import SimilarGoods from "./content/SimilarGoods";

const proxyStore = new Store({
    patchStrategy: patchDeepDiff
});

var temp = document.createElement('div');

setTimeout(() => {
    proxyStore.ready().then(() => {

        if (proxyStore.state.options.show_similar_goods_on_hypermarkets_pages && (isWildberriesPage() || isOzonPage())) {
            let page = isWildberriesPage() ? 'wildberries' : 'ozon';
            render(
                <Provider store={proxyStore}>
                    <SimilarGoods page={page}/>
                </Provider>,
                temp
            );
        } else {
            return false
        }
    });
}, 100)


/**
 * check current page is wildberries product page
 * @returns {boolean}
 */
function isWildberriesPage() {
    let regStr = `^.*(wildberries.ru).*(/detail.aspx)$`,
        regular = new RegExp(regStr, 'gi');

    return regular.test(window.location.origin + window.location.pathname);
}

/**
 * check current page is ozon product page
 * @returns {boolean}
 */
function isOzonPage() {
    let regStr = `^.*(ozon.ru)(/product/|/context/detail/id/).*$`,
        regular = new RegExp(regStr, 'gi');

    return regular.test(window.location.origin + window.location.pathname);
}

if (isWildberriesPage()) {

    window.onload = () => {

        // grab the container
        let container = document.getElementsByClassName("card-right");

        if (container.length) {
            container[0].append(temp);
        }
    }
}

if (isOzonPage()) {

    setTimeout(() => {
        let characteristicsBlock = document.querySelector("div[data-widget='webCharacteristics']");
        if (characteristicsBlock) {
            let container = characteristicsBlock.closest("div[data-widget='row']");

            if (container) {
                container.after(temp);
            }
        }
    }, 2000)
}

