import React, {useEffect, useState} from 'react';
import {connect} from "react-redux";
import ReactDOMServer from 'react-dom/server';

import {
    ADD_URL_TO_QUEUE,
    SAVE_ITEM_DATA,
    SET_OFFER_PAUSED,
    REQUEST_ACTIVE_TAB_ITEM_DATA,
    UPDATE_ACTIVE_TAB_ITEM_DATA,
    REQUEST_SIMILAR_ITEMS,
    REQUEST_SELLER_DATA,
    SET_SELLER_DATA,
    CLEAR_SELLER_DATA,
    CHECK_AFFILIATE,
    CLEAR_SIMILAR_ITEMS,
    REQUEST_CHECK_AFFILIATE,
    SET_POPUP_PAGE,
    YANDEX_DOMAINS,
    GOOGLE_DOMAIN,
    INSERT_SEARCHERS_CASHBACK_DATA,
    REQUEST_LINKS_DATA,
    SEND_LINK_DATA,
    SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION
} from "../../background/constant";

import TopLineActivate from "./TopLineActivate";

import {Offers} from '../../background/reducers/offers';
import chromeLib from '../../lib/BrowserApi';

window.chromeLib = chromeLib;
import microType from "../../lib/ParserMicrotipe";
import aliSimilarItems from "../../lib/AliSimilarItems";
import aliSellerData from "../../lib/AliSellerData";
import aliOrderAction from "../../lib/AliOrderActions";
import parcelAction from "../../lib/AliParcelActions";
import helper from "../../lib/Helper";
import searchersPage from "../../lib/SearchersPage";
import linksTooltips from "../../lib/LinksTooltips";
import TopLineSettings from "./TopLineSettingsModal";
import LinkTooltip from "../content/LinkTooltip";
import IncreasedCashbackPopup from "../content/IncreasedCashbackPopup";
import eventsObserver from "../../lib/EventsObserver";
import PopupCashbackActivated from "./PopupCashbackActivated";
import PopupRemindCashbackActivation from "./PopupRemindCashbackActivation";

function TopLine({offers, dispatch, options, lang, showToplineActivated, productDisabledDomainList}) {

    const [isActive, setIsActive] = useState(false);
    const [isOurOffer, setIsOurOffer] = useState(false);
    const [isForceClose, setIsForceClose] = useState(false);
    const [offer, setOffer] = useState({id: 0});
    const [tabDomainDisabled, setTabDomainDisabled] = useState(false);
    const [popupPage, setPopupPage] = useState('shop');
    const [isAliexpress, setIsAliexpress] = useState(helper.isAliepress(window.location.origin));
    const [location, setLocation] = useState('');
    const [isShowToplineActivated, setIsShowToplineActivated] = useState(showToplineActivated);

    function calcActiveOffer(offerParameter, offersLib) {

        setIsActive(helper.isActiveCashback(offerParameter));
        setIsOurOffer(offerParameter !== undefined && offerParameter.active === 'active' && offerParameter.load_to_ext === '1');

        setOffer(offerParameter);
        setTabDomainDisabled(productDisabledDomainList.indexOf(helper.getUrlInfo(window.location.origin).domain) > -1);
    }

    useEffect(() => {
        let offersLib = new Offers(offers);
        let offerParam = offersLib.getOffer(window.location.origin);
        setIsShowToplineActivated(helper.isNeedToShopToplineActivated(offerParam));
    }, [isShowToplineActivated]);

    useEffect(() => {

        if (isAliexpress) {
            aliOrderAction.init();
            if (options.parcel_track_auto_send) {
                parcelAction.init();
            }
        }

        let offersLib = new Offers(offers);
        let offerParam = offersLib.getOffer(window.location.origin);
        calcActiveOffer(offerParam, offersLib);

        chromeLib.sendMessage(ADD_URL_TO_QUEUE);

        // страница поисковиков
        initSearchers();

        eventsObserver.eventResizeObserver((entries) => {
            setTimeout(() => {
                // links to partner shops
                initLinksTooltips(offersLib);
            }, 2000);
        });


        collectAliSimilarItems();

        chromeLib.onMessageContent((message) => {
            message = message || {request: 'none'};
            switch (message.request) {
                /**
                 * Запрос на обновление данных по товару из активной вкладке
                 */
                case REQUEST_ACTIVE_TAB_ITEM_DATA:
                    microType.microParse();
                    let isMicroTypeExists = microType.isDataExists();

                    dispatch({
                        type: UPDATE_ACTIVE_TAB_ITEM_DATA,
                        product: {
                            ...microType.getProductData(),
                            fullData: isMicroTypeExists,
                            preloader: false
                        }
                    });

                    if (isMicroTypeExists) {
                        setPopupPage('product');

                        dispatch({
                            type: SET_POPUP_PAGE,
                            value: 'product',
                            force: false
                        });
                    } else {
                        dispatch({
                            type: SET_POPUP_PAGE,
                            value: popupPage,
                            force: false
                        });
                    }

                    setTimeout(() => {
                        if (isAliexpress && isMicroTypeExists) {
                            chromeLib.sendMessage(CHECK_AFFILIATE);
                        }
                    }, 10)
                    break;

                /**
                 * insert data to page
                 */
                case INSERT_SEARCHERS_CASHBACK_DATA:

                    if (parseFloat(message.percent) > 0) {
                        switch (message.provider) {

                            case 'yandex':
                                insertYandexLine(message);
                                break;

                            case 'google':
                                insertGoogleLine(message);
                                break;

                            default:
                                break;
                        }
                    }
                    break;

                /**
                 * Запрос на обновление данных по блоку проверки продавца со страницы товара
                 */
                case REQUEST_SELLER_DATA:
                    // очищаем объект
                    dispatch({
                        type: CLEAR_SELLER_DATA,
                    });

                    aliSellerData.init((data) => {
                        dispatch({
                            type: SET_SELLER_DATA,
                            value: data
                        });
                        chromeLib.sendMessage(SET_SELLER_DATA, data)
                    });
                    break;

                /**
                 * Запрос на пересчет иконки по аффилярному товару
                 */
                case REQUEST_CHECK_AFFILIATE:
                    chromeLib.sendMessage(REQUEST_CHECK_AFFILIATE)
                    break;

                /**
                 *  запрос данных по похожим товарам в активной вкладке
                 */
                case REQUEST_SIMILAR_ITEMS:
                    collectAliSimilarItems();
                    break;

                case REQUEST_LINKS_DATA:
                    // console.log("MESSAGE: ", message);
                    linksTooltips.renderShortLinks(message);
                    break;

                case SEND_LINK_DATA:
                    // console.log("MESSAGE: ", message);
                    let targetLink = linksTooltips.reWriteShortLinkAttributes(message);
                    // to re-build activation popup for the element
                    eventsObserver.triggerMouseEnter(targetLink);
                    break;

                default:
                    break;
            }
        })


    }, [offer]); // empty deps - only on mount component


    useEffect(() => {
        microType.microParse();

        let isMicroTypeExists = microType.isDataExists();

        // если это страница товара
        if (isMicroTypeExists) {
            setPopupPage('product');

            // microType.isDataExists()
            // отправка данных о товаре в background
            setTimeout(() => {
                chromeLib.sendMessage(SAVE_ITEM_DATA, {product: microType.getProductData()})
            }, 7500);

            setTimeout(() => {

                if (!tabDomainDisabled) {
                    dispatch({
                        type: UPDATE_ACTIVE_TAB_ITEM_DATA,
                        product: {
                            ...microType.getProductData(),
                            fullData: isMicroTypeExists,
                            checkAffiliateStatus: isAliexpress ? -2 : 1,
                            preloader: false
                        }
                    });
                }

                setTimeout(() => {
                    if (isAliexpress) {
                        chromeLib.sendMessage(CHECK_AFFILIATE);
                    }
                }, 10)

            }, 10)

        } else {
            dispatch({
                type: "CLEAR_ACTIVE_TAB_ITEM_DATA"
            });
        }

        dispatch({
            type: SET_POPUP_PAGE,
            value: popupPage,
            force: false
        });

        let timerId = setInterval(() => useForceUpdate(window.location.href), 5000);
        return () => {
            clearInterval(timerId);
        }

    }, [location]);

    function useForceUpdate(href) {
        setLocation(href);
    }

    function initLinksTooltips(offersLib) {
        if (!options.link_tooltips_show) {
            return;
        }

        linksTooltips.init(offersLib);
    }

    /**
     * Выводим подсказки о магазинах для поисковиков google и yandex
     */
    function initSearchers() {

        if (!options.google_and_yandex_show) {
            return;
        }

        var domain = helper.getUrlInfo(window.location.origin).domain,
            searcher = '';

        if (YANDEX_DOMAINS.indexOf(domain) > -1) {
            searcher = 'yandex';
        } else if (domain.indexOf(GOOGLE_DOMAIN) > -1) {
            searcher = 'google';
        }

        if (searcher === '')
            return false;

        searchersPage.init(searcher);
    }


    function insertGoogleLine(data) {

        var target = document.getElementsByClassName("g")[data.li_eq];

        if (target.previousSibling && target.previousSibling.classList.contains("g-mb"))
            return;

        if (target.getAttribute('id') === 'imagebox_bigimages')
            return;

        var wrapper = document.createElement('div');
        wrapper.innerHTML = getGoogleLine(data);

        target.before(wrapper.firstChild);
    }

    function insertYandexLine(data) {

        var target = document.getElementsByClassName("serp-item")[data.li_eq];

        // если уже вставлен компонент
        if (target.previousSibling && target.previousSibling.classList.contains("serp-item-mb"))
            return;

        var wrapper = document.createElement('div');
        wrapper.innerHTML = getYandexLine(data);

        target.before(wrapper.firstChild);
    }

    function getYandexLine(data) {

        return ReactDOMServer.renderToStaticMarkup(
            <li className={'serp-item-mb'}>
                <div className={'organic bno'}>
                    <h2 className={'organic__title-wrapper typo typo_text_l typo_line_m'}>
                        <div className={'favicon'}>
                            <div className={'favicon__icon'}/>
                        </div>
                        <div className={'organic__url-text'}>
                            <span>{lang.content_searchers_line_with_cashback}</span>
                            {
                                data.count_categories > 1 &&
                                <span>{' ' + lang.content_searchers_line_up_to}</span>
                            }

                            <b className={'fifteen'}>{' ' + data.percent + (data.currency !== '%' ? ' ' + data.currency : data.currency) + ' '}</b>
                            <span>{lang.content_searchers_line_from}</span>
                            <b>{' ' + lang.content_searchers_line_megabonus}</b>
                        </div>
                    </h2>
                </div>
            </li>
        );
    }

    function getGoogleLine(data) {
        return ReactDOMServer.renderToStaticMarkup(
            <div className={'g-mb'}>
                <div>
                    <span className={'img'}/>
                    <span>{lang.content_searchers_line_with_cashback}
                        {
                            data.count_categories > 1 &&
                            <span>{' ' + lang.content_searchers_line_up_to}</span>
                        }
                        <b className={'fifteen'}>{' ' + data.percent + (data.currency !== '%' ? ' ' + data.currency : data.currency) + ' '}</b>
                        <span>{lang.content_searchers_line_from}</span>
                        <b>{' ' + lang.content_searchers_line_megabonus}</b>
                    </span>
                </div>
            </div>
        );
    }

    /**
     * Сбор данных по похожим товарам Aliexpress
     */
    function collectAliSimilarItems() {
        dispatch({type: CLEAR_SIMILAR_ITEMS})

        if (isAliexpress) {
            aliSimilarItems.init();
        }
    }

    function isTopLineDisallowOffer() {
        let topLineDisallowOffers = ['booking', 'aliexpress'];
        return (offer && offer.offer_name && topLineDisallowOffers.includes(offer.offer_name.toLowerCase()));
    }

    function isPausedActivateTopLine() {
        return offer !== undefined && offer.pausedTopline !== undefined && offer.pausedTopline > (~~(Date.now() / 1000))
    }

    function isForcedToBeClosed() {
        let isEnabledByOptionSetting = options.info_about_sites_with_cashback;
        return !isEnabledByOptionSetting || isForceClose;
    }

    function handleHideTimeout(minutes = 15) {
        dispatch({
            type: SET_OFFER_PAUSED,
            value: (~~(Date.now() / 1000)) + minutes * 60,
            url: window.location.origin
        });
        dispatch({
            type: SET_SHOW_TOPLINE_POPUP_REMIND_CASHBACK_ACTIVATION,
            value: true
        });
        handleHideActivateTopLine();
    }

    function handleHideActivateTopLine() {
        setIsForceClose(!isForceClose);
    }


    return (
        <React.Fragment>
            {
                !isTopLineDisallowOffer() &&
                <div id="notice-wrapper"
                     className={isOurOffer && !isActive && !isAliexpress ? 'show' : ""}>
                    {
                        !isForcedToBeClosed() &&
                        isOurOffer &&
                        (!isActive) &&
                        !isPausedActivateTopLine() &&
                        <TopLineActivate
                            handleHideTimeout={(minutes) => {
                                handleHideTimeout(minutes)
                            }}
                        />
                    }
                    <TopLineSettings
                        handleHideActivateTopLine={() => handleHideActivateTopLine()}
                    />
                    {
                        isShowToplineActivated &&
                        <PopupCashbackActivated/>
                    }
                    <PopupRemindCashbackActivation/>
                </div>
            }
            <LinkTooltip/>
            <IncreasedCashbackPopup/>
        </React.Fragment>
    );

}

const mapStateToProps = (state) => {
    return {
        lang: state.lang,
        offers: state.offers,
        showToplineActivated: state.activeTabOffer.showToplineActivated,
        productDisabledDomainList: state.productDisabledDomainList.list,
        options: state.options,
    }
};

export default connect(mapStateToProps)(TopLine);
