import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux'
import chromeLib from "../../lib/BrowserApi";
import {
    GET_INCREASED_CASHBACK_DATA,
    GOOGLE_EVENT_SEND,
    HOME,
    HOST,
    REGISTER_LINK,
    SET_INCREASED_CASHBACK_POPUP_DELAY,
    SET_SIMILAR_PRODUCTS,
} from "../../background/constant";

function IncreasedCashbackPopup({domains, shops, lang, token, delayAllDomains, dispatch}) {

    const [closed, setClosed] = useState(isClosed());
    const [location, setLocation] = useState('');
    const targetPage = getTargetPage();

    useEffect(() => {

        document.addEventListener('click', (e) => {

            setTimeout(() => {
                let el = e.target.closest('a');
                if (el) {
                    useForceUpdate(el.getAttribute('href'))
                }
            }, 500)
        });

        return () => {
            document.removeEventListener('click', () => useForceUpdate());
        }

    }, [location]); // empty deps - only on mount component

    if (!checkAllowed())
        return null;

    function useForceUpdate(href) {
        setLocation(href);
    }

    /**
     * show popup or not
     * @returns {boolean|boolean|*}
     */
    function checkAllowed() {

        if (!domains.length)
            return false;

        let regStr = `^.*(${domains.join('|')})/?$`,
            regular = new RegExp(regStr, 'gi');

        return regular.test(window.location.origin + window.location.pathname) && shops.length && !closed;
    }

    function getTargetPage() {
        let targetPageHost = window.location.hostname;
        switch (true) {
            case (targetPageHost.indexOf("ozon.") !== -1):
                return 'ozon page';
            case (targetPageHost.indexOf("wildberries.") !== -1):
                return 'wildberries page';
            case (targetPageHost.indexOf("dns-shop.") !== -1):
                return 'dns-shop page';
            default:
                return 'other page';
        }
    }

    /**
     * нажимали закрыть на этом сайте
     * @returns {*|boolean}
     */
    function isClosed() {
        let now = new Date().valueOf();

        return delayAllDomains > now;
    }

    /**
     * click
     * @param shop
     */
    function clickOnShopLine(shop) {
        if (token.length === 0) {
            window.open(REGISTER_LINK)
        } else {

            window.open('https://' + HOST + shop.link + '?com=ext_incr_popup&increase=' + shop.id);

            // update increased cashback list
            setTimeout(() => {
                chromeLib.sendMessage(GET_INCREASED_CASHBACK_DATA);
            })
        }

        chromeLib.sendMessage(GOOGLE_EVENT_SEND, {
            category: 'pop up increased cash back',
            action: shop.offer_name_en.toLowerCase() + ' click',
            eventLabel: targetPage
        });
    }

    function checkSliceSum(percent, currency) {
        if (currency !== '%') {
            percent = parseInt(percent);
        }
        return percent + currency;
    }

    function closePopup() {
        setClosed(true);

        dispatch({
            type: SET_INCREASED_CASHBACK_POPUP_DELAY
        })
    }

    return (
        <div className={'mb-incr-cash-popup'}>
            <img className={'mb-incr-cash-popup__close'}
                 src={'https://cdn.megabonus.com/images/user/popups/cancel_icon.svg'}
                 alt={'close-icon'}
                 onClick={() => closePopup()}/>
            <div className={'mb-incr-cash-popup__logo'}>
                <img src={chromeLib.getPath(`icons/popup/logo-${lang.lang === 'ru' ? 'ru' : 'en'}.png`)}
                     alt={'logo'}
                     onClick={() => window.open(HOME)}/>
            </div>

            <div className={'mb-incr-cash-popup__texts'}>
                <p className={'mb-incr-cash-popup__texts__title'}>{lang.increased_cashback_popup_title}</p>
                <div className={'mb-incr-cash-popup__texts__description'}>
                    <p className={'mb-incr-cash-popup__texts__description__text'}>{lang.increased_cashback_popup_description}</p>
                    <div className={'mb-incr-cash-popup__texts__description__arrow'}/>
                </div>
            </div>

            <div className={'mb-incr-cash-popup__shops'}>

                {
                    shops.map((shop, index) => {

                        chromeLib.sendMessage(GOOGLE_EVENT_SEND, {
                            category: 'pop up increased cash back',
                            action: shop.offer_name_en.toLowerCase() + ' view',
                            eventLabel: targetPage
                        });

                        return (
                            <div className={'mb-incr-cash-popup__shops__shop'}
                                 key={index}
                                 onClick={(e) => clickOnShopLine(shop)}
                            >
                                <div className={'mb-incr-cash-popup__shops__shop__fire-icon'}/>
                                <img src={shop.logo} alt={shop['offer_name_' + lang.lang]}/>
                                <div className={'mb-incr-cash-popup__shops__shop__percents'}>
                                    {lang.increased_cashback_popup_up_to + ' '}
                                    <span>{checkSliceSum(shop.max_percent, shop.offer_percent_currency)}</span>
                                    {' ' + checkSliceSum(shop.max_percent_mult, shop.offer_percent_currency)}
                                </div>
                            </div>
                        )
                    })
                }
            </div>

        </div>
    );
}


const mapStateToProps = state => {
    return {
        domains: state.increasedCashback.domains,
        shops: state.increasedCashback.shops,
        delayAllDomains: state.increasedCashback.delayAllDomains,
        lang: state.lang,
        token: state.user.token,
    }
};

export default connect(mapStateToProps)(IncreasedCashbackPopup)
