import React from 'react';
import {connect} from 'react-redux'
import helper from "../../lib/Helper";

import {GOOGLE_EVENT_SEND, HOST, REGISTER_LINK} from "../../background/constant";
import chromeLib from "../../lib/BrowserApi";

function SimilarProduct({lang, product, page, token}) {

    /**
     * click
     */
    function clickOnProduct() {

        if (token.length === 0) {
            window.open(REGISTER_LINK)
        } else {
            window.open(`https://smartcart.${HOST}/card/${product.id}?sub1=extSimProd`);
        }

        let eventLabel;
        switch(page) {
            case 'wildberries':
                eventLabel = 'wildberries page';
                break;
            case 'ozon':
                eventLabel = 'ozon page';
                break;
            default:
                eventLabel = 'other page';
                break;
        }
        chromeLib.sendMessage(GOOGLE_EVENT_SEND, {
            category: 'similar items block',
            action: 'item click',
            eventLabel: eventLabel
        });
    }

    let img;

    return (
        <div
            onClick={() => clickOnProduct()}
            className={"mb-similar-product-item"}>
            <div className={"mb-similar-product-item__img"}>
                <img src={product.img}
                     alt={product.title}
                     ref={el => img = el}
                     onError={() => {
                         img.src = 'https://cdn.megabonus.com/images/smartcart/default_img.png'
                     }}
                />
            </div>
            <div className={"mb-similar-product-item__shop-name"}>{product.offer_name}</div>
            <div className={"mb-similar-product-item__price"}>
                {helper.getUserFriendlyPrice(product.price, product.currency)}
            </div>
            <div className={"mb-similar-product-item__cashback"}>
                <div className={"mb-similar-product-item__cashback__mb-icon"}/>
                <span>{lang.popup_similar_component_cashback + ' ' + lang.link_tooltip_up_to + ' '
                + product.user_percent + product.user_currency}</span>
            </div>
            <div className={"mb-similar-product-item__title"}>{product.title}</div>
        </div>
    )
}


const mapStateToProps = state => {
    return {
        lang: state.lang,
        token: state.user.token,
    }
};

export default connect(mapStateToProps)(SimilarProduct)

