import jQuery from 'jquery';
import ParsePage from "./ParsePage";
import helper from "../Helper";

const $ = window.jQuery = jQuery;

class ParsePageAmazon extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    this.getPriceAndCurrency();
    this.getImage();
    this.getImagesList();
    this.getTitle();
    this.getDescription();
    this.getBrand();
    this.getCategory();
    this.getRating();
    this.getCharacteristics();
    this.getReviews();

    return this.getProductData();
  }

  getPriceAndCurrency() {
    let unformattedPriceObj = $('.a-color-price');
    if (unformattedPriceObj.length > 0) {
      for (let i = 0; i < unformattedPriceObj.length; i++) {
        let unformattedPriceText = unformattedPriceObj.eq(i).text().trim();
        if (unformattedPriceText === "") {
          continue;
        }
        // possible unformattedPrice format "RUB 99,999,999.99", but mya be interval "$12.90 - $25.00" or line-break
        unformattedPriceText = unformattedPriceText.split(/[-\n]/)[0].trim();
        // to avoid issues with floating point (3.00000000001)
        unformattedPriceText = unformattedPriceText.replace('.00', '');
        // console.log('TEST: ', unformattedPrice);
        if (!this.price || this.price === "0") { // possible price format "99,999,999.99"
          this.price = this.getDataFromDOM(unformattedPriceText, /[0-9]{1,3}(,[0-9]{3})*(\.[0-9]+)*/, true);
          this.currency = unformattedPriceText.replace(this.price.toString(), '').trim();
          // price postprocessing
          this.price = this.price.replace(',', '');
          // currency postprocessing
          if (this.currency === '$') {
            this.currency = 'USD';
          }
        }
        break;
      }
    }
  }

  getImage() {
    let imageObj = $('.imgTagWrapper img');
    if (imageObj.length > 0) {
      this.img = imageObj.attr('src');
    }
    if (!this.img) {
      imageObj = $('.gc-design-image-wrapper img');
      if (imageObj.length > 0) {
        this.img = imageObj.attr('src');
      }
    }
  }

  getImagesList() {
    if (this.img_all.length > 0) {
      return;
    }
    let imagesListObj = $('#altImages img');
    if (imagesListObj.length > 0) {
      let img_all_tmp = this.img_all;
      let images_max_count = 5;
      let images_count = 0;
      $.each(imagesListObj, function(index, image) {
        if (typeof $(image).attr('src') === 'string') {
          let imgSrc = $(image).attr('src');
          // don't need this icon
          if (imgSrc.includes('360_icon')) {
            return;
          }
          // to make full images from shortcuts
          if (imgSrc.includes('_US40')) {
            imgSrc = imgSrc.replace('_US40', '');
          }
          img_all_tmp.push(imgSrc);
          images_count++;
          if (images_count >= images_max_count) {
            return false;
          }
        }
      });
      this.img_all = img_all_tmp;
    }
  }

  getTitle() {
    let titleObj = $('#productTitle');
    if (titleObj.length > 0) {
      this.title = titleObj.eq(0).text().trim();
    }
  }

  getDescription() {
    if (this.description.length > 0) {
      return;
    }
    let descriptionObj = $('#feature-bullets .a-list-item');
    let desc_all_tmp = this.description;
    let desc_max_count = 5;
    let desc_count = 0;
    $.each(descriptionObj, function(index, descriptionItem) {
      desc_all_tmp += $(descriptionItem).eq(0).text();
      desc_count++;
      if (desc_count >= desc_max_count) {
        return false;
      }
    });
    this.description = desc_all_tmp;
  }

  getBrand() {
    if (!this.brand) {
      let brandObj = $('#mbc');
      if (brandObj.length > 0 && brandObj.attr('data-brand') !== undefined) {
        this.brand = brandObj.attr('data-brand');
      }
    }
    if (!this.brand) {
      let brandObj = $('#bylineInfo');
      if (brandObj.length > 0) {
        let rawBrand = brandObj.eq(0).text(); //match(/Brand: (\S+)/)
        rawBrand = rawBrand.match(/Brand: (.+)$/) || rawBrand.match(/Visit the (.+) Store/);
        if (rawBrand) {
          this.brand = rawBrand[1].trim();
        }
      }
    }
  }

  getCategory() {
    // Category
    if (!this.category) {
      let breadCrumbDivider = $('.a-breadcrumb-divider');
      if (breadCrumbDivider.length > 0 && breadCrumbDivider.eq(-1).prev().length > 0) {
        this.category = breadCrumbDivider.eq(-1).prev().text().trim();
      }
    }
  }

  getRating() {
    let ratingObj = $('#acrPopover');
    let tmpRating = 0;
    if (ratingObj.length > 0) {
      let titleRating = ratingObj.attr('title');
      if (titleRating && titleRating.length > 0) {
        tmpRating = parseFloat(titleRating);
      }
      if (tmpRating > 0) {
        this.rating = tmpRating;
      }
    }
  }

  getCharacteristics() {
    if (this.characteristics.length > 0) {
      return;
    }
    let characteristicsTable = $('#productOverview_feature_div');
    if (characteristicsTable.length > 0) {
      let characteristicsItems = $('span.a-size-base', characteristicsTable);
      let characteristicTmpResults = this.characteristics
      let characteristicObj = { attrName:'', attrValue:'' };
      let characteristicText = '';
      $.each(characteristicsItems, function(index, characteristicsItem) {
        characteristicText = $(characteristicsItem).eq(0).text();
        if (!characteristicText || characteristicText.length < 1) {
          return;
        }
        // console.log(characteristicText);
        if (helper.isOdd(index)) {
          characteristicObj.attrValue = characteristicText;
        }
        else {
          characteristicObj.attrName = characteristicText;
        }
        if (characteristicObj.attrName !== '' && characteristicObj.attrValue !== '') {
          characteristicTmpResults.push(characteristicObj);
          characteristicObj = { attrName:'', attrValue:'' };
        }
      });
      this.characteristics = characteristicTmpResults;
    }
  }

  getReviews() {
    if (this.reviews.length > 0) {
      return;
    }
    let reviewsObj = $('#cm-cr-dp-review-list');
    let reviewsItems = reviewsObj.find('.review');
    let reviewsTmp = [];
    let reviews_max_count = 5;
    let reviews_count = 0;
    if (reviewsItems.length > 0) {
      $.each(reviewsItems, function (index, reviewItem) {
        let reviewTmp = {};

        // console.log(reviewItem);

        let reviewId = $(reviewItem).attr('id');
        if (!reviewId) {
          return;
        }
        reviewTmp.id = reviewId;

        let userName = $('.a-profile-name', $(reviewItem));
        if (userName.length === 0) {
          return;
        }
        reviewTmp.username = userName.eq(0).text();

        let stars = $('.a-link-normal', $(reviewItem));
        if (stars.length === 0) {
          return;
        }
        reviewTmp.stars = parseInt($(stars).attr('title'));

        let text = $('.reviewText span', $(reviewItem));
        if (text.length === 0) {
          return;
        }
        reviewTmp.text = $(text).eq(0).text();

        let date = $('.review-date', $(reviewItem));
        if (date.length === 0) {
          return;
        }
        date = $(date).eq(0).text();
        if (date.indexOf(' on ') < 0) {
          return;
        }
        date = date.substring(date.indexOf(' on ') + 4);
        reviewTmp.date = helper.getDateFromString(date);

        let likes = $('.cr-vote-text', $(reviewItem));
        if (likes.length === 0) {
          return;
        }
        reviewTmp.likes = parseInt($(likes).eq(0).text());

        reviewTmp.images = [];
        let imagesObj = $('.review-image-tile-section img', $(reviewItem));
        if (imagesObj.length > 0) {
          $.each(imagesObj, function(index, imageObj) {
            reviewTmp.images.push($(imageObj).attr('data-src'));
          });
        }

        reviewsTmp.push(reviewTmp);

        reviews_count++;
        if (reviews_count >= reviews_max_count) {
          return false;
        }
      });
    }
    this.reviews = reviewsTmp;
  }

}

const parsePageAmazon = new ParsePageAmazon();
export default parsePageAmazon;
