import chromeLib from "./BrowserApi";
import helper from "./Helper";
import ReactTooltip from "react-tooltip";

import {REQUEST_LINKS_DATA} from "../background/constant";

var md5 = require('md5');

class LinksTooltips {

    constructor() {
        this.originalLinks = [];
        this.links = {};
        this.targetLinkRegex = "";
        this.partnerLinkRegex = "";
        this.offers = {}
    }

    init(offersLib) {
        // check if allowed web pages
        this.setOffers(offersLib);
        if (!this.checkCurrentPageAllowedDomain()) {
            return;
        }
        // check if allowed links exists
        this.getLinks();
        if (!this.originalLinks || this.originalLinks.length === 0) {
            return;
        }
        // process direct links
        this.renderDirectLinks(this.originalLinks);
        // process shorted links
        this.getPatterns();
        this.setLinks(this.originalLinks);
        this.sendLinksToProcessHrefs();
    }

    getLinks() {
        this.originalLinks = [...document.getElementsByTagName('a')];
    }

    setLinks() {
        let linksFiltered = {};

        this.originalLinks.forEach((link, index) => {
            // skip links with onclick event as they are not simple links, but active elements
            let onclick = link.getAttribute('onclick');
            // skip links if mb-checked = 1, as they were checked of previous iteration
            let checked = link.getAttribute('mb-checked');
            let linkHref = link.getAttribute("href");
            if (onclick || (checked && checked === "1") || !linkHref) {
                return false;
            }

            // mark new links as checked
            link.setAttribute('mb-checked', "1"); // to avoid checking the same link several times
            link.setAttribute('data-tip', ""); // null - to do not show tooltip

            linkHref = decodeURIComponent(linkHref);
            // console.log('-- LINK ', linkHref);
            // check if it's "our" potential link (such links will be called after on mouse over the link)
            let matchTargetLinkHref = linkHref.match(this.targetLinkRegex);
            if (matchTargetLinkHref && (matchTargetLinkHref[0] || matchTargetLinkHref[1])) {
                let targetLink = matchTargetLinkHref[1] ?? matchTargetLinkHref[0];
                let targetLinkParsed = null;

                try {
                    targetLinkParsed = new URL(targetLink);
                } catch {
                    // console.warn('Not URL: ', targetLink);
                    return false;
                }

                if (targetLinkParsed && !this.checkLinkSimilarDomainWithContentPage(targetLinkParsed.hostname)) {
                    // console.log('---- potentially OUR LINK ', targetLink, matchTargetLinkHref);
                    this.setLinkAttributes(link, index, targetLink, false);
                } else {
                    // console.log('---- the similar domain with content page: ', targetLinkParsed.hostname);
                    return false;
                }

                // check if it's "our" link very likely (such links will be called after page load)
                let matchPartnerLinkHref = targetLink.match(this.partnerLinkRegex);
                if (matchPartnerLinkHref && matchPartnerLinkHref[0]) {
                    // console.log('------ very likely OUR LINK ');
                    // link.setAttribute('mb-link-index', index); // save index to assign data after follow the link and return from background
                    linksFiltered['"' + index + '"'] = matchPartnerLinkHref[0];
                }
            }
        });

        ReactTooltip.rebuild();

        this.links = linksFiltered;
        // console.log('-------------- Short Links to render:', this.links);
    }

    setOffers(offersLib) {
        this.offers = offersLib;
    }

    getPatterns() {
        let partnerLinkRegexList = [
            // "ali\\w+\\.\\w{2,5}\\/[\\w_]+",
            //alitems.com/g/adFGH; aliclick.shop/r1/c1/OneTwo; ali.pub/q; a.alii.pub/asdd;
            "(\\w{1,10}\\.)?a{1,2}l{1,2}i{1,2}\\w*\\.\\w{2,5}\\/(\\w{1,10}\\/){0,3}[\\w_]+",
            "shp\\.pub\\/[\\w_]+",
            "zozi\\.ru\\/[\\w.]+",
            "vk\\.cc\\/[\\w_]+",
            "clc\\.to\\/[\\w_]+",
            "okl\\.lt\\/[\\w_]+",
            "bit\\.ly\\/[\\w_]+",
            "is\\.gd\\/[\\w_]+",
            "got\\.by\\/[\\w_]+",
            "s\\.click\\.aliexpress\\.com\\/e\\/[\\w_]+",
            "s\\.click\\.aliexpress\\.com\\/deep_link.htm\\?[\\w-._~:/?#@!$&()*+,;=[\\]%]+"
        ];
        this.partnerLinkRegex = new RegExp("https?:\\/\\/(" + partnerLinkRegexList.join('|') + ")");

        // example: https://vk.com/away.php?to1=http://ali.shop/r/c/1qp?p_id=1&params=f&to2=https://ali.shop/1qp?p_id=2
        this.targetLinkRegex = new RegExp("(https?:\\/\\/([\\w-]+.){1,3}\\w+(\\/[\\w?=\\-.]+){0,10})(&|\\?|$)");
    }

    sendLinksToProcessHrefs() {
        // console.log('links: ',this.links);
        if (Object.getOwnPropertyNames(this.links).length === 0) {
            return null;
        }
        chromeLib.sendMessage(REQUEST_LINKS_DATA, {
            links: JSON.stringify(this.links)
        });
    }

    /**
     * not show tooltips on these pages
     * @returns {boolean}
     */
    checkCurrentPageAllowedDomain() {
        // do not show in searchers, competitors and some aliexpress (as there are 'kostili' in checkIsOurOffer for ali)
        let excludedDomains = ['yandex', 'google', 'bing', 'megabonus', 'alibonus', 'backit', 'aliexpress'];
        let domain = helper.getUrlInfo(window.location.origin).domain.split('.')[0];
        // do not show in partner page
        return !excludedDomains.includes(domain) && !this.checkIsOurOffer(window.location.origin);
    }

    /**
     * @param url
     * @returns {boolean}
     */
    checkIsOurOffer(url) {
        let offer = this.offers.getOffer(url);
        return offer !== undefined && offer.active === 'active' && offer.load_to_ext === '1';
    }

    /**
     * @param urlHostname
     * @returns {boolean}
     */
    checkLinkSimilarDomainWithContentPage(urlHostname) {
        let contentPageUrl = location.hostname;
        return (
            urlHostname.includes(contentPageUrl)
            || contentPageUrl.includes(urlHostname)
            || this.checkIncludesDomainFromSubdomain(urlHostname, contentPageUrl)
        );
    }

    checkIncludesDomainFromSubdomain(urlHostname, contentPageUrl) {
        let urlHostnameSegments = urlHostname.split('.');
        // example: urlHostname = 'secure.iherb.ru', contentPageUrl = 'ru.iherb.ru'
        if (urlHostnameSegments.length > 2) {
            // minus one left segment, example: before 'secure.iherb.ru', after 'iherb.ru'
            urlHostnameSegments.shift();
            // example: 'secure.iherb.ru'.includes('iherb.ru')
            return contentPageUrl.includes(urlHostnameSegments.join('.'));
        }
        return false;
    }

    renderShortLinks(links) {
        // console.log(this.originalLinks, links);
        let urlsArray = Object.values(links);
        let urlsKeys = Object.keys(links);
        try {
            for (let i = 0; i < urlsArray.length; i++) {
                let urlKey = parseInt(urlsKeys[i].replace('"', ''));
                if (isNaN(urlKey)) {
                    // console.warn('Url index is not number: ', urlsKeys[i]);
                    continue;
                }
                this.setLinkAttributes(this.originalLinks[urlKey], urlKey, urlsArray[i]);
            }
        } catch (e) {
            console.error('ERROR: ', e);
        }

        ReactTooltip.rebuild();
    }

    reWriteShortLinkAttributes(linkInfo) {
        // let tooltipData = JSON.stringify(this.getDataByLink(linkInfo.redirectedUrl)); // in case of class selector approach
        // let targetLinkClassName = md5(linkInfo.originalUrl); // in case of class selector approach
        // let targetLink = [...document.getElementsByClassName(targetLinkClassName)]; // in case of class selector approach
        // targetLink.forEach((element, index) => element.setAttribute('data-tip',tooltipData)); // in case of class selector approach
        // todo: approach with index is not very stable, we can use combination of index and class selector approach
        let tooltipData = JSON.stringify(this.getDataByLink(linkInfo));
        let targetLink = this.originalLinks[linkInfo.index];
        targetLink.setAttribute('data-tip', tooltipData);
        return targetLink;
    }

    renderDirectLinks(links) {
        links.filter((link, index) => {
            let href = link.getAttribute('href');

            if (!href || href.length === 0)
                return false;

            return this.checkIsOurOffer(href.toLowerCase());

        }).map((link, index) => {
            let href = link.getAttribute('href');
            this.setLinkAttributes(link, index, href);
        });

        ReactTooltip.rebuild();
    }

    getDataByLink(linkInfo, withShop = true) {
        let offer = null; //redirectedUrl
        let url = linkInfo.redirectedUrl;

        if (withShop) {
            let shop = this.offers.getOffer(url ? url.toLowerCase() : null);
            if (shop === undefined) {
                // console.warn('Undef shop for: ', url);
                return {};
            } else if (!shop.active || shop.active !== 'active') {
                // not active cashback in shop
                return {};
            }
            offer = {
                offer_name: shop.offer_name,
                offer_percent_currency: shop.offer_percent_currency,
                user_percent: shop.user_percent,
                aff_link: shop.aff_link,
                show_tooltip_on_sites: shop.show_tooltip_on_sites || 1
            };

            if (shop.show_tooltip_on_sites !== undefined && parseInt(shop.show_tooltip_on_sites) === 0) {
                return {};
            }
        }

        return {
            index: linkInfo.index ?? null,
            offer: offer,
            link: url
        };
    }

    setLinkAttributes(link, index, url, withShop = true) {
        let Data = this.getDataByLink({
            redirectedUrl: url,
            index: index,
        }, withShop);

        link.setAttribute('data-tip', JSON.stringify(Data));
        link.setAttribute('data-for', 'mb-link-tooltip');
        link.setAttribute('mb-checked', "1"); // to avoid re-write  data-tip from short links algorithms
        // link.classList.add(md5(url)); // in case of class selector approach
    }

}

const linksTooltips = new LinksTooltips();
export default linksTooltips;
