import React, {useState} from 'react';
import {connect} from 'react-redux'
import {
    HOME,
    SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED,
    SET_SHOW_TOPLINE_SETTINGS_MODAL
} from "../../background/constant";
import TopLineHelpModal from "./TopLineHelpModal";

function PopupCashbackActivated({lang, showSettingsModal, dispatch}) {

    const [isShow, setIsShow] = useState(true);
    const [isShowHelpModal, setIsShowHelpModal] = useState(false);

    if(!isShow) {
        return null;
    }

    function closePopupCashbackActivated() {

        setIsShow(false);

        dispatch({
            type: SET_OFFER_SHOW_TOPLINE_POPUP_CASHBACK_ACTIVATED,
            value: false,
            url: window.location.origin
        });
    }

    function showModalSettings() {
        dispatch({
            type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
            page: window.location.href,
            value: !showSettingsModal
        })
        closeModalHelp();
    }

    function closeModalHelp() {
        setIsShowHelpModal(false);
    }

    return (
        <div id="notice-3" style={{
            height: 'auto', top: 0, left: 0
        }}>
            <strong className="logoAB"
                    style={{background: "url(https://cdn.megabonus.com/images/extension/gray-logo-".concat((lang.lang === 'ru' ? lang.lang : 'en')).concat(".svg) left 50% / auto 14px no-repeat")}}
                    data-spm-anchor-id="a2g0o.detail.0.i7.442c3568pjrdFc"
                    onClick={() => {
                        window.open(HOME)
                    }}
            ><a href="#"> </a></strong>
            <div className="btn-holder center-text">
                        <span className="ext-mb-title"
                              dangerouslySetInnerHTML={{__html: lang.activation_is_finished}}/>
            </div>
            <div className="actions">
                <a href="#" className="btn-help-notice"
                   style={
                       {
                           background: "url(https://cdn.megabonus.com/images/extension/gray-help.svg) 0% 0% / cover no-repeat"
                       }
                   }
                   onClick={() => {
                       dispatch({
                           type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
                           value: false,
                           page: window.location.href
                       })
                       setIsShowHelpModal(true);
                   }}
                > </a>
                {
                    <TopLineHelpModal onClose={closeModalHelp} show={isShowHelpModal}/>
                }

                <a href="#" className="btn-settings-notice"
                   style={
                       {background: "url(https://cdn.megabonus.com/images/extension/gray-settings.svg) 0% 0% / cover no-repeat"}
                   }
                   onClick={() => {
                       showModalSettings();
                   }}
                />

                <a href="#" className="btn-close-topline"
                   onClick={() => {
                       closePopupCashbackActivated();
                   }}
                />

            </div>
        </div>
    );
}


const mapStateToProps = (state) => {
    return {
        lang: state.lang,
        showSettingsModal: state.topline.showSettingsModal,
    }
};

export default connect(mapStateToProps)(PopupCashbackActivated)