import jQuery from 'jquery';
import ParsePage from "./ParsePage";
import helper from "./../Helper";

const $ = window.jQuery = jQuery;

class ParsePageAliexpressRu extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    let ParamsObj = $('script[id="__AER_DATA__"]');
    if (ParamsObj.length > 0) {
      // console.log('!!! __AER_DATA__');
      let data = JSON.parse(ParamsObj.eq(0).html().replace('};', '}'));
      // console.log(data);
      if (data.widgets && data.widgets[0] && data.widgets[0].children) {
        let widgets = data.widgets[0].children;
        for (let i = 0; i < widgets.length; i++) {
          let widget = widgets[i];
          if (!widget.widgetId) {
            continue;
          }
          // console.log(widget);
          switch (true) {
            case /Grid/.test(widget.widgetId):
              if (widget.children && widget.children.length > 0) {
                for (let j = 0; j < widget.children.length; j++) {
                  if (/Product/.test(widget.children[j].widgetId)) {
                    this.getPriceAndCurrency(widget.children[j]);
                    this.getTitle(widget.children[j]);
                    this.getDescription(widget.children[j]);
                    this.getImagesList(widget.children[j]);
                    this.getOrdersCount(widget.children[j]);
                    this.getRating(widget.children[j]);

                    break;
                  }

                  if (/Container/.test(widget.children[j].widgetId)) {
                    let lowContainerWidgets = widget.children[j];
                    for (let jj = 0; jj < lowContainerWidgets.children.length; jj++) {
                      if (/TabsPdp/.test(lowContainerWidgets.children[jj].widgetId)) {
                        let LowTabsPdpWidgets = lowContainerWidgets.children[jj];
                        for (let jjj = 0; jjj < LowTabsPdpWidgets.children.length; jjj++) {
                          if (/Container/.test(LowTabsPdpWidgets.children[jjj].widgetId)) {
                            let lowLowContainerWidgets = LowTabsPdpWidgets.children[jjj];
                            for (let jjjj = 0; jjjj < lowLowContainerWidgets.children.length; jjjj++) {
                              if (/Characteristics/.test(lowLowContainerWidgets.children[jjjj].widgetId)) {
                                let characteristicsWidget = lowLowContainerWidgets.children[jjjj];

                                this.getCharacteristics(characteristicsWidget);

                                break;
                              }
                            }
                            break;
                          }
                        }
                        break;
                      }
                    }
                    break;
                  }
                }
              }

              //
              break;
            case /BreadCrumbs/.test(widget.widgetId):
              this.getCategory(widget);
              break;
            case /HeaderPDP/.test(widget.widgetId):
              this.getSellerData(widget);
              break;
            default:

              break;
          }
        }
      }

    }

    return this.getProductData();
  }

  getPriceAndCurrency(widget) {
    if (widget.props && widget.props.price) {
      let priceObj = widget.props.price;
      if (priceObj.maxActivityAmount && priceObj.maxActivityAmount.value) {
        this.price = priceObj.maxActivityAmount.value;
        this.currency = priceObj.maxActivityAmount.currency;
      }
      else if (priceObj.minActivityAmount && priceObj.minActivityAmount.value) {
        this.price = priceObj.minActivityAmount.value;
        this.currency = priceObj.minActivityAmount.currency;
      }
    }
  }

  getTitle(widget) {
    if (widget.props && widget.props.name && widget.props.name.length > 0) {
      this.title = widget.props.name.trim();
    }
  }

  getDescription(widget) {
    if (widget.props && widget.props.description && widget.props.description.length > 0) {
      this.title = widget.props.description.trim();
    }
  }

  getImagesList(widget) {
    if (
        (!this.img_all || this.img_all.length === 0)
        && widget.props && widget.props.gallery && widget.props.gallery.length > 0) {
      let images = [];
      for (let img = 0; img < widget.props.gallery.length; img ++) {
        if (widget.props.gallery[img].imageUrl) {
          images.push(widget.props.gallery[img].imageUrl.trim());
        }
      }
      if (images.length > 0) {
        this.img_all = images;
        this.getImage();
      }
    }
  }

  getImage() {
    if (this.img_all && this.img_all.length > 0) {
      this.img = this.img_all[0];
    }
  }

  getOrdersCount(widget) {
    if (widget.props && widget.props.tradeInfo && widget.props.tradeInfo.tradeCount) {
      this.orders_count = parseInt(widget.props.tradeInfo.tradeCount);
    }
  }

  getCharacteristics(widget) {
    if ((!this.img_all || this.img_all.length === 0)
      && widget.props && widget.props.char && widget.props.char.length > 0) {
      let characteristics = [];
      for (let ch = 0; ch < widget.props.char.length; ch++) {
        let characteristicObj = {
          'attrName': widget.props.char[ch].title,
          'attrValue': widget.props.char[ch].value
        };
        characteristics.push(characteristicObj);
        if (widget.props.char[ch].title === 'Название бренда' && widget.props.char[ch].value.length > 0) {
          this.getBrand(widget.props.char[ch].value)
        }
      }
      if (characteristics.length > 0) {
        this.characteristics = characteristics;
      }
    }
  }

  getBrand(brand) {
    if (!this.brand) {
      this.brand = brand.trim();
    }
  }

  getRating(widget) {
    if (widget.props && widget.props.rating && widget.props.rating.middle) {
      // average star count
      this.rating = parseFloat(widget.props.rating.middle);
    }
  }

  getCategory(widget) {
    if (widget.props && widget.props.breadcrumbs && widget.props.breadcrumbs > 0) {
      let breadcrumbs = widget.props.breadcrumbs;
      let categoryObj = breadcrumbs.length > 1 ? breadcrumbs[breadcrumbs.length - 1] : breadcrumbs[0];
      if (categoryObj.name && categoryObj.name.length > 0) {
        this.category = categoryObj.name.trim();
      }
    }
  }

  getSellerData(widget) {
    if (widget.props && widget.props.shop) {
      let sellerDataObj = widget.props.shop;
      this.seller_data.id = parseInt(sellerDataObj.storeNum);
      this.seller_data.url = this.urlPrepare(sellerDataObj.url);
      this.seller_data.name = typeof sellerDataObj.name === 'string' ? sellerDataObj.name : '';
      this.seller_data.positive_num = parseInt(sellerDataObj.positiveReviews.number);
      let positiveRate = parseFloat(sellerDataObj.positiveReviews.percentages);
      this.seller_data.negative_num = 0;
      if (this.seller_data.positive_num > 0 && positiveRate > 0 && positiveRate < 100) {
        this.seller_data.negative_num = Math.round(this.seller_data.positive_num * (100 - positiveRate) / positiveRate);
      }
      let currentTimestamp = helper.getNowTimestamp();
      let shopCreatedTimestamp = parseInt(sellerDataObj.dateOpening);
      let secondsInYear = 60 * 60 * 24 * 365;
      this.seller_data.opened_years = Math.floor((Math.abs(currentTimestamp - shopCreatedTimestamp))/secondsInYear);
    }
  }

}

const parsePageAliexpressRu = new ParsePageAliexpressRu();
export default parsePageAliexpressRu;
