import jQuery from 'jquery';
import ParsePage from "./ParsePage";

const $ = window.jQuery = jQuery;

class ParsePageLdJson extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    let ldJsonObj = $('script[type="application/ld+json"]');
    if (ldJsonObj.length > 0) {

      for (let i = 0; i < ldJsonObj.length; i++) {
        let data = JSON.parse(ldJsonObj.eq(i).html().replace('};', '}'));

        this.getPriceAndCurrency(data);
        this.getImage(data);
        this.getDescription(data);
        this.getTitle(data);
        this.getBrand(data);
        this.getRating(data);
        this.getReviewsCount(data);
        this.getCategory(data);

      }
    }

    return this.getProductData();
  }

  getPriceAndCurrency(data) {
    if (data.offers !== null && data.offers !== undefined) {
      // Price and currency
      if (Array.isArray(data.offers)) {
        this.currency = data.offers[0].priceCurrency || this.currency;
        this.price = String(data.offers[0].price || this.price).split('.00')[0];
        this.price = String(data.offers[0].lowPrice || this.price).split('.00')[0];
      } else {
        this.currency = data.offers.priceCurrency || this.currency;
        this.price = String(data.offers.price || this.price).split('.00')[0];
        this.price = String(data.offers.lowPrice || this.price).split('.00')[0];
      }
    }
  }

  getImage(data) {
    this.img = data.image || this.img;
  }

  getDescription(data) {
    this.description = data.description || this.description;
  }

  getTitle(data) {
    this.title = data.name || this.title;
  }

  getBrand(data) {
    if (!this.brand && data.brand !== undefined) {
      if (data.brand.name !== undefined) {
        this.brand = data.brand.name.trim();
      }
      if (!this.brand) {
        this.brand = data.brand.trim();
      }
    }
  }

  getRating(data) {
    if (!this.rating && data.aggregateRating !== undefined && data.aggregateRating.ratingValue !== undefined) {
      this.rating = parseFloat(data.aggregateRating.ratingValue);
    }
  }

  getReviewsCount(data) {
    // Reviews count (= orders count)
    if (!this.orders_count && data.aggregateRating !== undefined && data.aggregateRating.reviewCount !== undefined) {
      this.orders_count = parseInt(data.aggregateRating.reviewCount);
    }
  }

  getCategory(data) {
    if (!this.category && data.category !== undefined) {
      if (data.category.name !== undefined) {
        this.category = data.category.name.trim();
      }
      if (!this.category) {
        this.category = data.category.trim();
      }
    }
    this.category = this.formatCategoryString(this.category);
  }

}

const parsePageLdJson = new ParsePageLdJson();
export default parsePageLdJson;
