import ParsePage from "./ParsePage";

class ParsePageAliexpress extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    let html = document.getElementsByTagName('html')[0].innerHTML;
    if (html.indexOf('window.runParams = ') > -1) {
      // console.log('!!! window.runParams');
      try {
        let string = html.split('window.runParams = ')[1].split('var GaData = ')[0].toString().replace('};', '}');

        this.getPriceAndCurrency(string);
        this.getBrand(string);
        this.getCategory(string);
        this.getImagesList(string);
        this.getTitle(string);
        this.getDescription(string);
        this.getOrdersCount(string);
        this.getRating(string);
        this.getCharacteristics(string);
        this.getSellerData(string);

      } catch (e) {console.log(e.toString())}

    } else {
      // console.log(html)
    }

    this.getUrl();

    return this.getProductData();
  }

  getPriceAndCurrency(string) {
    if (/priceModule/.test(string)) {
      if (/minAmount/.test(string)) {
        this.price = this.getDataFromDOM(string, /"minAmount"?:?{?"currency"?:?".{1,10}"?,?"formatedAmount?"?:?".{1,30}"?,?"value"?:?([0-9]+([.][0-9]*)?|[.][0-9]+)}/);
        this.currency = this.getDataFromDOM(string, /"minAmount":{.*currency":\s?"(\w+)"/);
      }
      if (/minActivityAmount/.test(string)) {
        this.price = this.getDataFromDOM(string, /"minActivityAmount"?:?{?"currency"?:?".{1,10}"?,?"formatedAmount?"?:?".{1,30}"?,?"value"?:?([0-9]+([.][0-9]*)?|[.][0-9]+)}/);
        this.currency = this.getDataFromDOM(string, /"minActivityAmount":{.*currency":\s?"(\w+)"/);
      }
    }
  }

  getBrand(string) {
    if (/specsModule/.test(string)) {
      if (/props/.test(string)) {
        let brand = this.getDataFromDOM(string, /attrNameId"?:"?2"?,"?attrValue"?:"(\S+?)"/).trim();
        if (brand) {
          this.brand = brand;
        }
      }
    }
  }

  getCategory(string) {
    let categoryObj = JSON.parse(this.getDataFromDOM(string, /breadCrumbPathList"?:(.+),"?crossLinkGroupList/).trim());
    if (categoryObj && categoryObj.length > 2 && categoryObj[categoryObj.length - 2].name !== undefined) {
      // category[0] - 'home' so 'length > 2'
      let category = categoryObj[categoryObj.length - 2].name.trim();
      if (category) {
        this.category = category;
      }
    }
  }

  getImagesList(string) {
    if (!this.img_all || this.img_all.length === 0) {
      let images = [];
      let imagesObj = JSON.parse(this.getDataFromDOM(string, /imagePathList"?:(.+),"?name"?:"ImageModule/).trim(), function (key, value) {
        if (value && typeof value === 'string') {
          images.push(value);
        }
      });
      if (images.length > 0) {
        this.img_all = images;
        this.getImage(images[0]);
      }
    }
  }

  getImage() {
    if (this.img_all && this.img_all.length > 0) {
      this.img = this.img_all[0];
    }
  }

  getTitle(string) {
    if (this.title && this.title.length > 0) {
      return;
    }
    if (/titleModule/.test(string)) {
      let title = this.getDataFromDOM(string, /"subject":"(.+)","tradeCount":/).trim();
      if (title) {
        this.title = title;
      }
    }
  }

  getDescription(string) {
    if (this.description && this.description.length > 0) {
      return;
    }
    if (/descriptionModule/.test(string)) {
      let description = this.getDataFromDOM(string, /"description":"(.+?)","features":/).trim();
      if (description) {
        this.description = description;
      }
    }
  }

  getOrdersCount(string) {
    this.orders_count = parseInt(this.getDataFromDOM(string, /"tradeCount":\s?"?(\w+)"?,"tradeCountUnit/));
  }

  getRating(string) {
    // average star count
    this.rating = parseFloat(this.getDataFromDOM(string, /"averageStar":\s?"?([\w.]+)"?,"averageStarRage/));
  }

  getCharacteristics(string) {
    let characteristicsObj = JSON.parse(this.getDataFromDOM(string, /SpecsModule","props":(.+)},"storeModule/).trim());
    if (characteristicsObj && characteristicsObj.length > 0) {
      this.characteristics = characteristicsObj;
    }
  }

  getSellerData() {
    let sellerDataObj = JSON.parse(this.getDataFromDOM(string, /storeModule":(.+),"titleModule/).trim());
    if (sellerDataObj && typeof sellerDataObj === 'object') {
      this.seller_data.id = parseInt(sellerDataObj.storeNum);
      this.seller_data.url = this.urlPrepare(sellerDataObj.storeURL);
      this.seller_data.name = typeof sellerDataObj.storeName === 'string' ? sellerDataObj.storeName : '';
      this.seller_data.positive_num = parseInt(sellerDataObj.positiveNum);
      let positiveRate = parseFloat(sellerDataObj.positiveRate);
      this.seller_data.negative_num = 0;
      if (this.seller_data.positive_num > 0 && positiveRate > 0 && positiveRate < 100) {
        this.seller_data.negative_num = Math.round(this.seller_data.positive_num * (100 - positiveRate) / positiveRate);
      }
      this.seller_data.opened_years = parseInt(sellerDataObj.openedYear);
    }
  }

  getUrl() {
    let url = this.url;
    if (url.indexOf('store/product') > -1) {
      url = url.replace('store/product/', 'item/');
      let splitArr = url.split('/');
      if (splitArr[splitArr.length - 1].split('_').length > 1) {
        splitArr[splitArr.length - 1] = splitArr[splitArr.length - 1].split('_')[1];
      }
      url = splitArr.join('/');
    }
    if (url.indexOf('tmall') === -1 && url.indexOf('/item/') > -1 && url.indexOf('.htm') && url.match(/http.*item\/([-\w]+\/)\w+.htm\w+/) !== null) {
      url = url.replace(url.match(/http.*item\/([-\w]+\/)\w+.htm\w+/)[1], '');
    }
    this.url = url;
  }

}

const parsePageAliexpress = new ParsePageAliexpress();
export default parsePageAliexpress;
