import React from 'react';
import {connect} from 'react-redux'
import chromeLib from '../../lib/BrowserApi';
import {
    REGISTER_LINK,
    REQUEST_LINK_DATA,
    SET_SHOW_TOPLINE_SETTINGS_MODAL
} from "../../background/constant";
import helper from "../../lib/Helper";
import ReactTooltip from "react-tooltip";

function LinkTooltip({lang, dispatch, token}) {

    /**
     *
     * @param shop
     * @returns {string}
     */
    function getTitle(shop) {
        return lang.link_tooltip_cash_in + " " + shop.offer_name + " " +
            lang.link_tooltip_up_to + " " + getPercent(shop);
    }

    /**
     *
     * @param shop
     * @returns {string}
     */
    function getPercent(shop) {

        switch (shop.offer_percent_currency) {
            case "USD":
                return "$" + shop.user_percent;
            case "%":
                return shop.user_percent + "%";
            case "RUB":
            case "RUR":
                return shop.user_percent + "₽";
            default:
                return shop.user_percent + " " + shop.offer_percent_currency;
        }
    }

    return (
        <ReactTooltip
            id='mb-link-tooltip'
            effect={'solid'}
            className='mb-link-tooltip'
            delayHide={300}
            clickable={true}
            getContent={
                (dataTip) => {

                    if (!dataTip)
                        return null;

                    let data = JSON.parse(dataTip)

                    if (!data || !data.link || (!data.index && (data.index !== 0)) ) // index=0 -- first element, valid
                        return null;

                    // single link fetch request
                    if (!data.offer) {
                        chromeLib.sendMessage(REQUEST_LINK_DATA, {
                            index: data.index,
                            link: data.link
                        });
                        return null;
                    }


                    if (data.offer.show_tooltip_on_sites !== undefined && parseInt(data.offer.show_tooltip_on_sites) === 0) {
                        return null;
                    }

                    return <React.Fragment>
                        <div className={'mb-link-tooltip__topline'}>
                            <img src={chromeLib.getPath('icons/content/transparent-gray-logo.svg')}
                                 width={16}
                                 height={16}
                                 alt={'Megabonus'}/>
                            <p>{getTitle(data.offer)}</p>
                            <img className={'mb-link-tooltip__topline__settings'}
                                 src={chromeLib.getPath('icons/content/settings_gray.svg')}
                                 width={16}
                                 height={16}
                                 onClick={(e) => {
                                     e.preventDefault();
                                     e.stopPropagation();
                                     dispatch({
                                         type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
                                         value: true,
                                         page: window.location.href
                                     })
                                 }}
                                 alt={'settings'}/>
                        </div>
                        <div className={'mb-link-tooltip__button'}>
                            <a href={'#'} onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                if (token.length === 0) {
                                    window.open(REGISTER_LINK)
                                } else {
                                    let redirectUrl = helper.getUrlToSmartcartTransitPage(data.offer, data.link);
                                    window.open(redirectUrl);
                                }
                            }}>{lang.link_tooltip_button}</a>
                        </div>
                    </React.Fragment>
                }

            }
        />
    );
}

const mapStateToProps = state => {
    return {
        lang: state.lang,
        token: state.user.token
    }
};
export default connect(mapStateToProps)(LinkTooltip)
