import jQuery from 'jquery';
import ParsePage from "./ParsePage";
import helper from "../Helper";

const $ = window.jQuery = jQuery;

class ParsePageShein extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    this.getPriceAndCurrency();
    this.getCategory();
    this.getBrand();
    this.getReviews();

    // Other parameters
    let html = document.getElementsByTagName('html')[0].innerHTML;
    try {
      if (html.indexOf('window.goodsDetailv2SsrData = ') > -1) {

        this.getImagesList(html);
        this.getCharacteristics(html);

      }
    }
    catch (e) {console.log(e.toString())}

    return this.getProductData();
  }

  getPriceAndCurrency() {
    let unformattedPriceObj = $('.product-intro__head-price span');
    if (unformattedPriceObj.length > 0) {
      let unformattedPriceText = unformattedPriceObj.eq(0).text().trim();
      let unformattedPrice = this.getDataFromDOM(unformattedPriceText, /[0-9]{1,3}([, ][0-9]{3})*([,.][0-9]+)*/, true);
      // exclude price to get currency
      this.currency = unformattedPriceText.replace(unformattedPrice,'').trim();
      // clear from price separators and postprocessing to common format (19,45 => 19.45) and resolve issue with floating point
      this.price = unformattedPrice.replace(/[ ,.](\d{3})/g,"$1").replace(',','.').replace('.00','');
    }
  }

  getCategory() {
    if (!this.category) {
      let categoryObj = $('.bread-crumb__item');
      if (categoryObj.length === 0) {
        categoryObj = $('.bread-crumb__item-link');
      }
      if (categoryObj.eq(-2).length > 0) {
        this.category = categoryObj.eq(-2).text().trim();
      }
    }
  }

  getBrand() {
    if (!this.brand) {
      this.brand = "shein";
    }
  }

  getReviews() {
    if (!this.reviews || this.reviews.length === 0) {
      let reviewsItemsList = $('.common-reviews__list-item');
      if (reviewsItemsList.length > 0) {
        let tempReviews = [];
        for (let index = 0; index < reviewsItemsList.length; index++) {
          let reviewsItem = reviewsItemsList[index];
          tempReviews[index] = {};
          tempReviews[index].id = $(reviewsItem).attr('data-comment-id');
          if ($(reviewsItem).attr('data-comment-id')) {
            tempReviews[index].id = $(reviewsItem).attr('data-comment-id');
          } else {
            continue;
          }
          let usernameObj = $(reviewsItem).find('.nikename');
          if (usernameObj.length === 1) {
            tempReviews[index].username = usernameObj.text();
          }
          let starsObj = $(reviewsItem).find('.rate-star');
          if (starsObj.length === 1 && starsObj.attr('aria-label')) {
            tempReviews[index].stars = starsObj.attr('aria-label').substr(-1);
          }
          let textObj = $(reviewsItem).find('.rate-des');
          if (textObj.length === 1) {
            tempReviews[index].text = textObj.text();
          }
          let dateObj = $(reviewsItem).find('.date');
          if (dateObj.length === 1) {
            tempReviews[index].date = helper.getDateFromString(dateObj.text().trim());
          }
          let likesObj = $(reviewsItem).find('.thumb-up');
          if (likesObj.length === 1) {
            tempReviews[index].likes = parseInt(likesObj.text().trim());
          }
          tempReviews[index].images = [];
          let imagesObj = $(reviewsItem).find('.common-reviews__list-item-pic');
          if (imagesObj.length === 1) {
            let imageListObj = $(imagesObj).find('img');
            for (let ind = 0; ind < imageListObj.length; ind++) {
              if (imageListObj.attr('data-src')) {
                tempReviews[index].images.push(imageListObj.attr('data-src'));
              }
            }
          }
        }
        this.reviews = tempReviews;
        // console.log('REVIEWS: ', this.reviews);
      }
    }
  }

  getImagesList(html) {
    if (!this.img_all || this.img_all.length === 0) {
      let images = this.img_all;
      let imagesUnformed = JSON.parse(this.getDataFromDOM(html, /detail_image"?:(.+),"?video_url/).trim());
      for (let i = 0; i < imagesUnformed.length; i++) {
        if (imagesUnformed[i].medium_image_webp && typeof imagesUnformed[i].medium_image_webp === 'string') {
          images.push(imagesUnformed[i].medium_image_webp);
        }
      }
      if (images.length > 0) {
        this.img_all = images;
      }
    }
  }

  getCharacteristics(html) {
    if (!this.characteristics || this.characteristics.length === 0) {
      let characteristicsObj = JSON.parse(this.getDataFromDOM(html, /productDetails":(.+)?],"comment"/).trim() + ']');
      for (let i = 0; i < characteristicsObj.length; i++) {
        if (characteristicsObj[i].attr_name
            && typeof characteristicsObj[i].attr_name === 'string'
            && characteristicsObj[i].attr_value
            && typeof characteristicsObj[i].attr_value === 'string') {
          this.characteristics.push({
            attrName: characteristicsObj[i].attr_name,
            attrValue: characteristicsObj[i].attr_value
          });
        }
      }
      // save spu attribute to take comments using this attributes from shein API by smartcart server
      let spu = this.getDataFromDOM(html, /productRelationID":"(.+?)"/).trim();
      // console.log('spu:', spu);
      if (spu && typeof spu === 'string' && spu.length < 36) {
        this.characteristics.push({
          attrName: 'spu',
          attrValue: spu
        });
      }
    }
  }

}

const parsePageShein = new ParsePageShein();
export default parsePageShein;
