import jQuery from 'jquery';
import ParsePage from "./ParsePage";

const $ = window.jQuery = jQuery;

class ParsePageMetaOG extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    this.getTitle();
    this.getDescription();
    this.getImage();
    this.getCurrency();
    this.getPrice();

    return this.getProductData();
  }

  getTitle() {
    if (this.title.length === 0) {
      this.title = $('meta[property="og:title"]').attr('content') || this.title;
    }
  }

  getDescription() {
    this.description = $('meta[property="og:description"]').attr('content') || this.description;
    this.description = $('meta[name="description"]').attr('content') || this.description;
  }

  getImage() {
    if (this.img.length === 0) {
      this.img = $('meta[property="og:image"]').attr('content') || this.img;
    }
  }

  getCurrency() {
    if (this.currency.length === 0) {
      let currencyObj = $('meta[property="og:price:currency"]');
      this.currency = currencyObj.attr('content') || this.currency;
    }

    if (this.currency.length === 0) {
      let metaCurrency = $('meta[property="product:price:currency"]');
      this.currency = metaCurrency.attr('content') || this.currency;
    }
  }

  getPrice() {
    if (!this.price || this.price === "0") {
      let priceObj = $('meta[property="og:price:amount"]');
      if (priceObj.length && priceObj.attr('content') && priceObj.attr('content').length)
        this.price = priceObj.attr('content') || this.price;
    }

    if (!this.price || this.price === "0") {
      let metaPrice = $('meta[property="product:price:amount"]');
      if (metaPrice && metaPrice.attr('content') && metaPrice.attr('content').length)
        this.price = this.formatPriceString(metaPrice.attr('content')) || this.price;
    }
  }

}

const parsePageMetaOG = new ParsePageMetaOG();
export default parsePageMetaOG;
