import React, { Component } from 'react';

import chromeLib from '../../lib/BrowserApi';
window.chromeLib = chromeLib;
import { connect } from "react-redux";
import {HOST} from "../../background/constant";


class TopLineHelpModal extends Component {
  static propTypes = {}

  static defaultProps = {
    show: false,
  }

  constructor (props) {
    super(props);
  }

  render () {
    return (
      <div className={"help-modal".concat(this.props.show  ? ' show' : '')}>
        <a className="close-icon" onClick={(e) => {
          this.props.onClose && this.props.onClose(e);
        }}></a>
        <h3>{this.props.lang.help_popup_title_1}</h3>
        <ol>
          <li>{this.props.lang.help_popup_instr_1}</li>
          <li>{this.props.lang.help_popup_instr_2}</li>
          <li>{this.props.lang.help_popup_instr_3}</li>
        </ol>
        <a href={"https://" + HOST + "/shop/aliexpress"} target="_blank">
          <span>{this.props.lang.help_popup_shop_link_info}</span><span>AliExpress</span>
        </a>
        <h3>{this.props.lang.help_popup_title_2}</h3>
        <p>{this.props.lang.help_popup_descr_2}</p>

        <ul>
          <li>
            <img src={chromeLib.getPath("icons/popup/ic_icons/ic_green_1.png")} alt=""/>
            <span>{this.props.lang.component_top_line_help_window_icon_text_1}</span>
          </li>
          <li>
            <img src={chromeLib.getPath("icons/popup/ic_icons/ic_attention_1.png")} alt=""/>
            <span>{this.props.lang.component_top_line_help_window_icon_text_2}</span>
          </li>
          <li>
            <img src={chromeLib.getPath("icons/popup/ic_icons/ic_orange_1.png")} alt=""/>
            <span>{this.props.lang.component_top_line_help_window_icon_text_3}</span>
          </li>
          <li>
            <img src={chromeLib.getPath("icons/popup/ic_icons/ic_grey_1.png")} alt=""/>
            <span>{this.props.lang.component_top_line_help_window_icon_text_4}</span>
          </li>
        </ul>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    lang: state.lang
  }
};

export default connect(mapStateToProps)(TopLineHelpModal);
