import parsePageProps from './parsers/ParsePageProps';
import parsePageLdJson from './parsers/ParsePageLdJson';
import parsePageMetaOG from './parsers/ParsePageMetaOG';
import parsePageAliexpress from './parsers/ParsePageAliexpress';
import parsePageAliexpressRu from './parsers/ParsePageAliexpressRu';
import parsePageAmazon from './parsers/ParsePageAmazon';
import parsePageShein from './parsers/ParsePageShein';

class ParserMicroTipe {

  constructor () {
    this.parsedData = {};
    this.dataComplete = false;
    this.dataEnouth = false;
  }

  getProductData () {
    return this.parsedData;
  }

  /**
   *  parse microparse
   *  Please note that page parse might be multiple times to fetch again dynamically loaded elements
   */
  microParse () {
    let url = window.location.href;

    if (url.indexOf("aliexpress.") > -1) {

      this.parsedData = parsePageAliexpress.parse(this.parsedData);
      // console.log('microParse aliexpress =>>', this.parsedData);
      if (parsePageAliexpress.checkDataComplete()) {
        // console.log('microParse aliexpress - COMPLETE');
        this.dataComplete = true;
      }

      else if (url.indexOf("aliexpress.ru") > -1) {
        this.parsedData = parsePageAliexpressRu.parse(this.parsedData);
        // console.log('microParse aliexpress RU =>>', this.parsedData);
        if (parsePageAliexpress.checkDataComplete()) {
          // console.log('microParse aliexpress RU - COMPLETE');
          this.dataComplete = true;
        }
      }

    }

    // separate amazon parser
    else if (url.indexOf("amazon.") > -1) {
      this.parsedData = parsePageAmazon.parse(this.parsedData);
      // console.log('microParse amazon =>>', this.parsedData);
      if (parsePageAmazon.checkDataComplete()) {
        // console.log('microParse amazon - COMPLETE');
        this.dataComplete = true;
      }
    }

    // separate shein parser
    else if (url.indexOf("shein.") > -1) {
      this.parsedData = parsePageShein.parse(this.parsedData);
      // console.log('microParse shein =>>', this.parsedData);
      if (parsePageShein.checkDataComplete()) {
        // console.log('microParse shein - COMPLETE');
        this.dataComplete = true;
      }
    }

    // check props attributes
    if (!this.dataComplete) {
      this.parsedData = parsePageProps.parse(this.parsedData);
      // console.log('microParse prop =>>', this.parsedData);
      if (parsePageProps.checkDataComplete()) {
        // console.log('microParse prop - COMPLETE');
        this.dataComplete = true;
      }
    }

    // check ld-json metadata
    if (!this.dataComplete) {
      this.parsedData = parsePageLdJson.parse(this.parsedData);
      // console.log('microParse ldJson =>>', this.parsedData);
      if (parsePageLdJson.checkDataComplete()) {
        // console.log('microParse ldJson - COMPLETE');
        this.dataComplete = true;
      }
    }

    // check meta:og attributes
    if (!this.dataComplete) {
      this.parsedData = parsePageMetaOG.parse(this.parsedData);
      // console.log('microParse metaOG =>>', this.parsedData);
      if (parsePageMetaOG.checkDataComplete()) {
        // console.log('microParse metaOG - COMPLETE');
        this.dataComplete = true;
      }
    }

    if (this.dataComplete) {
      this.dataEnouth = true;
    }
    else if (this.checkDataEnough()) {
      this.dataEnouth = true;
    }
    // console.log('Is data enough to send?', this.dataEnouth)

    return this;
  }

  checkDataEnough() {
    return (parseFloat(this.parsedData.price) > 0
        && this.parsedData.title.length > 0
        && this.parsedData.currency.length > 0
        && this.parsedData.img.length > 0);
  }

  isDataExists () {
    return this.dataEnouth;
  }

}

const microType = new ParserMicroTipe();
export default microType;
