import React from 'react';
import {render} from 'react-dom';
import {Provider} from 'react-redux';
import ContentMain from './containers/ContentMain';

import {Store} from 'webext-redux';
import patchDeepDiff from 'webext-redux/lib/strategies/deepDiff/patch';

const proxyStore = new Store({
    patchStrategy: patchDeepDiff
});

var extWindowBoxObj = document.createElement('div');
extWindowBoxObj.id = 'ext-megabonus-main-content';
extWindowBoxObj.className = 'ext-megabonus-top-line';
document.body.prepend(extWindowBoxObj);

setTimeout(() => {
    proxyStore.ready().then(() => {
        render(
            <Provider store={proxyStore}>
                <ContentMain/>
            </Provider>,
            extWindowBoxObj
        );
    });
}, 100)


