import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux'
import chromeLib from "../../lib/BrowserApi";
import {
    GET_SIMILAR_PRODUCTS,
    GOOGLE_EVENT_SEND,
    HOME,
    SET_SHOW_TOPLINE_SETTINGS_MODAL,
    SET_SIMILAR_PRODUCTS,
} from "../../background/constant";
import SimilarProduct from "../components/SimilarProduct";
import NextArrow from "../components/slick/NextArrow";
import PrevArrow from "../components/slick/PrevArrow";
import Slider from "react-slick";

function SimilarGoods({options, lang, page, dispatch}) {

    const [products, setProducts] = useState([]);
    const [location, setLocation] = useState('');
    const settings = {
        dots: false,
        infinite: false,
        className: "mb-slick-slider",
        speed: 500,
        variableWidth: true,
        slidesToShow: 2,
        slidesToScroll: 3,
        lazyLoad: 'progressive',
        nextArrow: <NextArrow/>,
        prevArrow: <PrevArrow/>,
    };

    if (!checkAllowed())
        return null;

    /**
     * show block or not
     * @returns {boolean|boolean|*}
     */
    function checkAllowed() {
        // not allowed in settings
        return options.show_similar_goods_on_hypermarkets_pages;
    }

    function getProductsData(brand, category) {
        if (brand || category) {
            chromeLib.sendMessage(GET_SIMILAR_PRODUCTS, {
                brand: brand,
                category: category
            });
        }
    }

    function useForceUpdate(href) {
        setLocation(href);
    }


    useEffect(() => {

        let brand, category, eventLabel;
        switch (page) {
            case 'wildberries':
                brand = document.querySelector('.brand-and-name .brand').innerText;
                // определение категории по тегам "C этим товаром искали"
                if (document.querySelector('.tags-group-list')) {
                    category = document.querySelector('.tags-group-list a').innerText;
                } else {
                    category = document.querySelector('.brand-and-name .name').innerText;
                }
                getProductsData(brand, category);

                eventLabel = 'wildberries page';

                break;
            case 'ozon':
                let breadCrumbs = document.querySelectorAll('div[data-widget="breadCrumbsPdp"] li span');

                brand = breadCrumbs[breadCrumbs.length - 1].innerText;
                category = breadCrumbs[breadCrumbs.length - 2].innerText;
                getProductsData(brand, category);

                eventLabel = 'ozon page';

                break;
            default:

                eventLabel = 'other page';

                break;
        }

        chromeLib.onMessageContent((message) => {
            message = message || {request: 'none'};
            switch (message.request) {

                case SET_SIMILAR_PRODUCTS:
                    setProducts(message.products);
                    break;

                default:
                    break;
            }
        })

        chromeLib.sendMessage(GOOGLE_EVENT_SEND, {
            category: 'similar items block',
            action: 'item view',
            eventLabel: eventLabel
        });

        document.addEventListener('click', (e) => {

            setTimeout(() => {
                useForceUpdate(e.target.closest('a').getAttribute('href'))
            }, 500)
        });

        return () => {
            document.removeEventListener('click', () => useForceUpdate());
        }

    }, [location]); // empty deps - only on mount component

    if (products.length === 0) {
        return null;
    }

    return (
        <div className={'mb-similar-goods-block'}>
            <div className={'mb-similar-goods-block__topline'}>
                <img src={chromeLib.getPath(`icons/popup/logo-${lang.lang === 'ru' ? 'ru' : 'en'}.png`)}
                     alt={'logo'}
                     className={'mb-similar-goods-block__topline__logo'}
                     onClick={() => window.open(HOME)}/>
                <img className={'mb-similar-goods-block__topline__settings'}
                     src={chromeLib.getPath('icons/content/settings_gray.svg')}
                     width={16}
                     height={16}
                     onClick={(e) => {
                         e.preventDefault();
                         e.stopPropagation();
                         dispatch({
                             type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
                             value: true,
                             page: window.location.href
                         })
                     }}
                     alt={'settings'}/>
            </div>

            <div className={'mb-similar-goods-block__texts'}>
                <p className={'mb-similar-goods-block__texts__title'}>{lang.similar_goods_content_title}</p>
                <p className={'mb-similar-goods-block__texts__description'}>{lang.similar_goods_content_description}</p>
            </div>

            <div className={'mb-similar-goods-block__goods'}>
                <Slider {...settings}>
                    {
                        products.map((product, index) => {
                            return (<SimilarProduct key={index} product={product} page={page}/>)
                        })
                    }
                </Slider>
            </div>

        </div>
    );

}


const mapStateToProps = state => {
    return {
        options: state.options,
        lang: state.lang,
    }
};

export default connect(mapStateToProps)(SimilarGoods)
