import React, {Component} from 'react';
import {connect} from "react-redux";
import {HOST, SET_OPTION_CHECKBOX, SET_SHOW_TOPLINE_SETTINGS_MODAL} from "../../background/constant";

import chromeLib from '../../lib/BrowserApi';

window.chromeLib = chromeLib;

class TopLineSettingsModal extends Component {
    static propTypes = {}

    constructor(props) {
        super(props);
    }

    handleChangeOptions = (e) => {
        this.props.dispatch({
            type: SET_OPTION_CHECKBOX,
            value: e.target.checked,
            name: e.target.name
        })
        if (e.target.name === 'info_about_sites_with_cashback') {
            this.props.handleHideActivateTopLine();
        }
    }

    handleChangeLang = (e) => {
        chromeLib.sendMessage('SET_LANG_DATA', {
            lang: e.target.value
        })
    }

    render() {
        return (
            <div style={{display:"none"}} className={"topline-settings-modal".concat((this.props.settingsModalPage.length === 0 || this.props.settingsModalPage === window.location.href) && this.props.showSettingsModal ? ' show' : '')}>
                <a className="close-icon"
                   onClick={(e) => {
                       this.props.dispatch({
                           type: SET_SHOW_TOPLINE_SETTINGS_MODAL,
                           value: !this.props.showSettingsModal,
                           page: window.location.href
                       })
                   }}/>
                <div className="loader">
                    <img src="https://cdn.megabonus.com/images/extension/preloader_yellow.svg" alt={''}/>
                </div>
                <h3>{this.props.lang.options_group_main}</h3>
                <div className="checkboxes">
                    <div>
                        <input type="checkbox" id="mb_sm_options_group_5"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.info_about_sites_with_cashback}
                               name="info_about_sites_with_cashback"/>
                        <label htmlFor="mb_sm_options_group_5">
                            <span>{this.props.lang.options_group_main_checkbox_7_text}</span>
                        </label>
                    </div>
                    <div>
                        <input type="checkbox" id="mb_sm_options_group_6"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.google_and_yandex_show}
                               name="google_and_yandex_show"/><label
                        htmlFor="mb_sm_options_group_6">
                        <span>{this.props.lang.google_and_yandex_show}</span></label>
                    </div>
                    <div>
                        <input type="checkbox" id="mb_sm_options_group_7"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.link_tooltips_show}
                               name="link_tooltips_show"/><label
                        htmlFor="mb_sm_options_group_7">
                        <span>{this.props.lang.link_tooltip_settings_title}</span></label>
                    </div>
                    <div>
                        <input type="checkbox" id="mb_sm_options_group_8"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.show_similar_goods_on_hypermarkets_pages}
                               name="show_similar_goods_on_hypermarkets_pages"/><label
                        htmlFor="mb_sm_options_group_8">
                        <span>{this.props.lang.similar_goods_on_hypermarkets_pages_settings_title}</span></label>
                    </div>
                    <div>
                        <input type="checkbox" id="mb_sm_options_group_9"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.parcel_track_auto_send}
                               name="parcel_track_auto_send"/><label
                        htmlFor="mb_sm_options_group_9">
                        <span>{this.props.lang.parcel_track_auto_send_title}</span></label>
                    </div>

                </div>
                <h3>{this.props.lang.options_group_language_setting}</h3>
                <select
                    onChange={(e) => this.handleChangeLang(e)} value={this.props.lang.lang}>
                    <option value="ru">Русский</option>
                    <option value="en">English</option>
                    <option value="es">Español</option>
                    <option value="pt">Português</option>
                </select>
                <h3>{this.props.lang.options_group_notify}</h3>
                <div className="checkboxes">
                    <div>
                        <input type="checkbox" id="mb_sm_options_notify_1"
                               onChange={(e) => this.handleChangeOptions(e)}
                               defaultChecked={!!this.props.options.cashback_notify} name="cashback_notify"/>
                        <label htmlFor="mb_sm_options_notify_1">
                            <span>{this.props.lang.options_group_notify_checkbox_1_text}</span>
                        </label>
                    </div>
                </div>

                <div className="bottom">
                    <a href={"https://" + HOST + "/user/ask"}
                       target="_blank">{this.props.lang.options_ask_a_question}</a>
                    <span className="copyright">© Megabonus.com {(new Date()).getFullYear()}</span></div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        options: state.options,
        lang: state.lang,
        showSettingsModal: state.topline.showSettingsModal,
        settingsModalPage: state.topline.settingsModalPage,
    }
};

export default connect(mapStateToProps)(TopLineSettingsModal);
