import jQuery from 'jquery';
import ParsePage from "./ParsePage";

const $ = window.jQuery = jQuery;

class ParsePageProps extends ParsePage {

  constructor() {
    super();
  }

  parse(parsedPreviouslyData) {
    this.getPreviouslyCollectedData(parsedPreviouslyData);

    this.getImage();
    this.getTitle();
    this.getBrand();
    this.getCategory();
    this.getDescription();
    this.getCurrency();
    this.getPrice();
    this.getVideo();

    return this.getProductData();
    // console.log('prop',this.prepareSaveData());
  }

  getImage() {
    this.img = this.checkSimplePropObj("image", this.img).trim();
    if (!this.img) {
      this.img = $('[itemprop="image"]').attr('src') || this.img;
    }
  }

  getTitle() {
    let titleMainObj = $('title');
    if (titleMainObj.length > 1) {
      titleMainObj = titleMainObj.eq(0);
    }
    if (titleMainObj && titleMainObj.text() && titleMainObj.text().length > 0)
      this.title = titleMainObj.text().trim() || this.title;
    if (!this.title) {
      this.title = this.checkSimplePropObj("name", this.title).trim();
    }
  }

  getBrand() {
    this.brand = this.checkSimplePropObj("brand", this.brand).trim();
  }

  getCategory() {
    this.category = this.checkSimplePropObj("category", this.category).trim();
    if (!this.category) {
      // first itemListElement type
      let breadcrumbObj = $('[itemprop="itemListElement"]');
      // second itemListElement type
      if (breadcrumbObj.length < 2) {
        breadcrumbObj = $('[property="itemListElement"]');
      }
      // breadcrumb is parsable?
      if (breadcrumbObj.length > 1) {
        // get item from the end of the tree
        let item = -2;
        this.category = breadcrumbObj.eq(item).text().length > 0 ? breadcrumbObj.eq(item).text().trim() : this.category;
        if (this.category && this.category === this.brand) {
          item -= 1;
          this.category = breadcrumbObj.eq(item).text().length > 0 ? breadcrumbObj.eq(item).text().trim() : this.category;
        }
      }
    }
    this.category = this.formatCategoryString(this.category);
  }

  getDescription() {
    this.description = this.checkSimplePropObj("description", this.description).trim();
  }

  getCurrency() {
    this.currency = this.checkSimplePropObj("priceCurrency", this.currency).trim();
  }

  getPrice() {
    this.price = this.checkSimplePropObj("lowPrice", this.price);
    if (!this.price || this.price === "0") {
      this.price = this.checkSimplePropObj("price", this.price);
    }
    this.price = this.formatPriceString(this.price);
  }

  getVideo() {
    let videos = $('video');
    if (videos !== undefined && videos.length > 0) {
      let img_all_tmp = this.img_all;
      let video_max_count = 3;
      let video_count = 0;
      $.each(videos, function (index, video) {
        if (typeof $(video).attr('src') === 'string') {
          img_all_tmp.push($(video).attr('src'));
          video_count++;
          if (video_count >= video_max_count) {
            return true;
          }
        }
      });
      this.img_all = img_all_tmp;
    }
  }

  checkSimplePropObj(propName, prevObj = '') {
    let simpleObj = $('[itemprop="' + propName + '"]');
    let newObj = prevObj;
    if (simpleObj && simpleObj.attr('content') && simpleObj.attr('content').length > 0)
      newObj = simpleObj.attr('content') || prevObj;
    if (simpleObj && simpleObj.text() && simpleObj.text().length > 0)
      newObj = simpleObj.text() || prevObj;
    return newObj;
  }

}

const parsePageProps = new ParsePageProps();
export default parsePageProps;
