import jQuery from 'jquery';
import chromeLib from "./BrowserApi";
import {GET_SEARCHERS_CASHBACK_INFO} from "../background/constant";

const $ = window.jQuery = jQuery;

class SearchersPage {

    constructor() {
        this.searcher = '';
        this.ya_goo_links_amount = 0;
    }

    init(searcher) {
        this.setSearcher(searcher);
        this.render();
    }

    setSearcher(searcher) {
        this.searcher = searcher;
    }

    render() {

        switch (this.searcher) {
            case "yandex":
                this.renderYandex();
                break;
            case "google":
                this.renderGoogle();
                break;
            default:
                break;
        }
    }


    renderYandex() {
        var li_items = $('.serp-item');
        if (this.ya_goo_links_amount !== li_items.length) {
            this.ya_goo_links_amount = li_items.length;

            var that = this;
            $.each(li_items, function (li_index, li) {

                if ($(li).find('.showSearchOfferLineextralinks').next().width() === undefined || $(li).find('.extralinks').next().width() < 20) {
                    var href = $(li).find('.organic__path a.link b').text().toLowerCase();
                    that.getCashbackInfoForSearcher(href, li_index);
                }
            });
        }
    }

    renderGoogle() {
        var search_items = $('.g');
        if (this.ya_goo_links_amount !== search_items.length) {
            this.ya_goo_links_amount = search_items.length;

            var that = this;

            $.each(search_items, function (index, item) {
                // element with class g and attribute data-hveid are skipped - not target
                let dataHveidAttr = $(item).attr('data-hveid');
                if (dataHveidAttr !== undefined && dataHveidAttr !== false) {
                    return;
                }
                var href = $(item).find('a:first').attr('href');
                that.getCashbackInfoForSearcher(href, index);
            });
        }
    }

    /**
     * @param url
     * @param li_eq
     */
    getCashbackInfoForSearcher(url, li_eq) {
        // console.log('url',url)
        chromeLib.sendMessage(GET_SEARCHERS_CASHBACK_INFO, {
            url: url,
            li_eq: li_eq,
            provider: this.searcher
        });
    }
}

const searchersPage = new SearchersPage();
export default searchersPage;
