import {
  ALI_ORDER_ACTION_CANCEL_ORDER,
  ALI_ORDER_ACTION_CONFIRM_SHIPPING,
  ALI_ORDER_ACTION_OPEN_DEBATE
} from "../background/constant";
import jQuery from 'jquery';
const $ = window.jQuery = jQuery;

class OrderActions {
  constructor () {
    this.isOrderPage = false;
    this.isOrderListPage = false;
    this.isAliPage = false;
    this.isDebatePage = false;

    if (window.location.origin.indexOf("aliexpress.com") > -1 || window.location.origin.indexOf("aliexpress.ru") > -1) {
      this.isAliPage = true;
    }
    if (window.location.href.indexOf("order_detail") > -1 && this.isAliPage) {
      this.isOrderPage = true;
    }

    if (window.location.href.indexOf("createIssueStep2.htm") > -1 && this.isAliPage) {
      this.isDebatePage = true;
    }

    if ((window.location.href.indexOf("orderList.htm") > -1 || window.location.href.indexOf("order_list.htm") > -1) && this.isAliPage) {
      this.isOrderListPage = true;
    }
  }

  init(){
    this.confirmOrderHandler();
    this.cancelOrderHandler();
    this.openDebateHandler();
  }

  cancelOrderHandler () {
    if (this.isOrderPage || this.isOrderListPage) {
      var cancelSent = [];
      $(document).on('click', '.button-cancelOrder', function (e) {
        var parent = $(this).closest('tbody');
        var url = new URL(window.location.href);
        var c_order_id = url.searchParams.get("orderId");
        if (!c_order_id) {
          c_order_id = parent.find('.order-info .first-row .info-body').text();
        }
        $(document).on('click', '#confirm_cpf[data-role=confirm]', function (e) {
          if (!cancelSent[c_order_id]) {
            cancelSent[c_order_id] = true;
            chromeLib.sendMessage(ALI_ORDER_ACTION_CANCEL_ORDER, { orderId: c_order_id })
          }
        })
      });

    }
  }

  confirmOrderHandler () {
    if (this.isOrderPage) {
      var sendReq1 = function () {
        var url = new URL(window.location.href);
        chromeLib.sendMessage(ALI_ORDER_ACTION_CONFIRM_SHIPPING, { orderId: url.searchParams.get("orderId") })
      }
      $(document).on('click', '.button-confirmOrderReceived', function (e) {
        $(document).on('click', '#confirm_cpf[data-role=confirm]', sendReq1);
      });

      $(document).on('click', function (e) {
        if (!$(e.target).closest(".button-confirmOrderReceived").length) {
          $(document).off('click', '#confirm_cpf[data-role=confirm]', sendReq1);
        }
      });
    }
  }

  openDebateHandler () {
    if (this.isDebatePage) {
      $(document).on('mousedown', '#submit', function (e) {
        var url = new URL(window.location.href);
        chromeLib.sendMessage(ALI_ORDER_ACTION_OPEN_DEBATE, { orderId: url.searchParams.get("orderId") })
      })
    }
  }
}

const orderActions = new OrderActions();
export default orderActions;
